import React from 'react';
import useGet from "../../utils/useGet";
import {Checkbox, Container, Divider, FormControlLabel, Grid, Icon, Paper, TextField} from "@material-ui/core";
import Master from "../Master";
import WithAdmin from "../_partials/hoc/WithAdmin";
import WithLoader from "../_partials/hoc/WithLoader";
import DetailEmploy from "./DetailEmploy";

const ShowPayRoll = ({match}) => {
    const id = match.params.id;
    const payroll = useGet(`/payroll/${id}`);
    return (
        <Master>
            <WithAdmin>
                <WithLoader loader={payroll.loader} text={'Cargando rol...'}>
                    <Container maxWidth='lg' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>payment</Icon> {(payroll.data.status === 1) ? 'Sueldo Pagado' : <div style={{color: 'red'}}>Pago Anulado</div>}</h4>
                            <Divider />
                        </div>
                        <Grid container spacing={3}>

                            {/*Nombre*/}
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label={'Empleado'}
                                    fullWidth
                                    value={(payroll.data.name + ' ' +payroll.data.surname ).toUpperCase()} readOnly/>
                            </Grid>

                            {/*Fecha*/}
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label={'Fecha'}
                                    fullWidth
                                    value={payroll.data.date} readOnly/>
                            </Grid>

                            {/*Monto*/}
                            <Grid item xs={12} sm={6}>
                                <TextField type='number' label='Monto $'
                                           name='amount'
                                           value={payroll.data.amount}
                                           readOnly fullWidth/>
                            </Grid>
                            {/*Bonus*/}
                            <Grid item xs={12} sm={6}>
                                <TextField type='number' label='+ Comisin $'
                                           name='bonus'
                                           readOnly value={payroll.data.bonus}
                                           fullWidth/>
                            </Grid>
                            {/*Extra*/}
                            <Grid item xs={12} sm={6}>
                                <TextField type='number' label='+ Valor Adicional $'
                                           name='extra'
                                           readOnly
                                           value={payroll.data.extra}
                                           fullWidth/>
                            </Grid>
                            {/*Descuento*/}
                            <Grid item xs={12} sm={6}>
                                <TextField type='number' label='- Descuento $'
                                           name='discount'
                                           readOnly
                                           value={payroll.data.discount}
                                           fullWidth/>
                            </Grid>
                            {/*Description*/}
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    id="description"
                                    label="Detalle | Observacin"
                                    multiline
                                    rows="4"
                                    fullWidth
                                    name='description' readOnly
                                    value={payroll.data.description}
                                />
                            </Grid>
                            {/*Adelnto y Concepto*/}
                            <Grid item xs={12} sm={6}>
                                <FormControlLabel
                                    control={
                                        <Checkbox checked={payroll.data.advance}
                                                  disabled
                                                  value="advance" />
                                    }
                                    label="ADELANTO"
                                />
                                <TextField
                                    id="concept"
                                    label="Concepto"
                                    multiline
                                    disabled
                                    rows="2"
                                    fullWidth
                                    name='concept'
                                    value={payroll.data.concept}
                                />
                            </Grid>

                            {
                                payroll.data && payroll.data.employ_id &&
                                <Grid item xs={12} md={12} style={{ marginTop: 10 }}>
                                    <Divider/>
                                    <br/>
                                    <DetailEmploy employ={payroll.data.employ_id} now={payroll.data.date}/>
                                </Grid>
                            }

                        </Grid>

                    </Container>
                </WithLoader>
            </WithAdmin>
        </Master>
    );
};

export default ShowPayRoll;
