import React, {useEffect, useRef, useState} from 'react';
import Axios from "axios";
import {Chip, Icon, IconButton, Tooltip} from "@material-ui/core";
import MUIDataTable from "mui-datatables";
import {Link} from "react-router-dom";
import PropTypes from 'prop-types';
import WithLoader from "../../_partials/hoc/WithLoader";
import {money, tableOptions} from "../../../config/config";
import {CREDIT_SHOW, toName} from "../../../config/urls";
import {processErrors, toDate} from "../../../utils/utils";
import {toast} from "react-toastify";
import DlgListCredit from ".././_dialog/DlgListCredit";
import CustomSearchRender from "../../_partials/CustomSearchRender";
import useHttp from "../../../utils/useHttp";


const TableListCredit = ({personId, userId, searchable}) => {
    const search = useHttp('get');
    const dialog = useRef();

    // state
    const [credits, setCredits] = useState([]);
    const [loader, setLoader] = useState(false);
    const [credit, setCredit] = useState({geo_lat: null, geo_lon: null});
    const [q, setQ] = useState(''); // para las bsquedas
    const [total, setTotal] = useState(0); // para paginar
    const [filters, setFilters] = useState({
        page: 0,
        rowPerPage: 15
    });


    // Obtener todos los creditos
    useEffect(() => {
        const getCredits = () => {
            let url = `/credit?page=${(filters.page+1)}&limit=${filters.rowPerPage}`;

            if (personId !== null)
                url+= `&person_id=${personId}`;

            setLoader(true);
            Axios.get(url).then(res => {
                if(res.ok) {
                    setCredits(res.data);
                    setTotal(res.total);
                }
            })
                .catch(err => toast.error(processErrors(err)))
                .finally(() => setLoader(false))
        };

        getCredits();
    }, [filters, personId]);

    // Ver localizacin GPS
    const viewLoc = (creditId) => {
        const cred = credits.find(c => c.id === creditId);
        if(!cred) return;
        setCredit(cred);
        dialog.current.openMap();
    };

    // confirmar el crdito para anular o finalizar
    const confirmCredit = (creditId, end = false) => {
        if (!creditId) {
            toast.warn('Vuelve a seleccionar crdito');
            return;
        }

        setCredit(creditId);

        if (!end)
            dialog.current.openInput(true);
        else
            dialog.current.openConfirm(true);
    };

    // buscar crditos
    const onSearch = (searchText) => {
        let url = `/credit?page=${(filters.page+1)}&limit=${filters.rowPerPage}`;
        if(searchText !== null)
            url = `/credit?q=${searchText}&limit=30`;
        setQ(searchText);
        search.request(url).then(res => {
            if (res.ok) {
                setCredits(res.data);
            }
        });
    };

    const columns = [
        {
            name: "client", label: 'CLIENTE',
            options: {
                filter: false,
                customBodyRender: (value, tableMeta) => {
                    return value ? value.toUpperCase() : 'Indefinido';
                }
            },
        },
        {name: "monto", label: 'MONTO', options: {filter: false,
                setCellHeaderProps: () => ({ align: 'right' }),
                setCellProps: () => ({ align: 'right' }),
                customBodyRender: (value) => {return <b>{money(value)}</b>;}}},
        {name: "utilidad", label: '%', options: {filter: false,
                display: false}},
        {name: "total_utilidad", label: 'TOTAL %', options: {filter: false, display: false,
                setCellHeaderProps: () => ({ align: 'right' }),
                setCellProps: () => ({ align: 'right' }),
                customBodyRender: (value) => {return <b>{money(value)}</b>;}}},
        {name: "total", label: 'TOTAL', options: {filter: false,
                setCellHeaderProps: () => ({ align: 'right' }),
                setCellProps: () => ({ align: 'right' }),
                customBodyRender: (value) => {return <b>{money(value)}</b>;}}},
        {name: "plazo", label: 'PLAZO', options: {filter: false}},
        {name: "cobro", label: 'COBRO', options: {filter: false, display: 'false'}},
        {name: "ruta", label: 'RUTA', options: {filter: false}},
        {
            name: "description", label: 'DETALLE',
            options: {
                filter: false,
                display: 'false',
                customBodyRender: (value) => {
                    return <small>{value}</small>
                }
            },
        },
        {
            name: "f_inicio", label: 'F. INICIO',
            options: {
                filter: false,
                customBodyRender: (value) => {
                    return toDate(value);
                }
            }
        },
        {
            name: "f_fin", label: 'F. FIN',
            options: {
                filter: false,
                customBodyRender: (value) => {
                    return toDate(value);
                }
            }
        },
        {
            name: 'status', label: 'ESTADO', options: {
                filter: true,
                customFilterListOptions: { render: v => `Estado: ${v}` },
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            { value === 1 && <Chip label="Activo" size={'small'} className={'bg-green'}/>}
                            { value === 0 && <Chip label="Anulado" size={'small'} className={'bg-red'}/>}
                            { value === 2 && <Chip label="Exitoso" size={'small'} className={'bg-info'}/>}
                        </div>
                    );
                }
            }
        },
        {
            label: 'OPCIONES', name: 'id',
            options: {
                filter: false, sort: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    const status = tableMeta.rowData[11];
                    return (
                        <>
                            <Tooltip title={'Ver detalles'}>
                                <IconButton component={Link}
                                            to={toName(CREDIT_SHOW, {id: value})}
                                            size={"small"}
                                            aria-label="Detalle">
                                    <Icon>chrome_reader_mode</Icon>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={'Ver Direccin'}>
                                <IconButton onClick={() => viewLoc(value)}
                                            size={"small"}
                                            aria-label="Detalle">
                                    <Icon>not_listed_location</Icon>
                                </IconButton>
                            </Tooltip>

                            { /*ANULA SOLO SI EL CRDITO ESTA ACTIVO*/
                                status === 1 &&
                                <>
                                    <Tooltip title={'Anular Crdito'}>
                                        <IconButton
                                            onClick={() => confirmCredit(value)}
                                            size={"small"}
                                            aria-label="Anular">
                                            <Icon>block</Icon>
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title={'Marcar como finalizado'}>
                                        <IconButton
                                            color={'secondary'}
                                            onClick={() => confirmCredit(value, true)}
                                            size={"small"}
                                            aria-label="Finalizar">
                                            <Icon>done-all</Icon>
                                        </IconButton>
                                    </Tooltip>
                                </>
                            }


                        </>
                    );
                },
            }
        }
    ];

    const options = {
        ...tableOptions,
        search: searchable,
        serverSide: true,
        searchText: q,
        rowsPerPage: filters.rowPerPage,
        page: filters.page,
        count: total,
        onTableChange: (action, tableState) => {
            switch (action) {
                case 'search':
                    onSearch(tableState.searchText);
                    break;
                case 'changePage':
                    setFilters({...filters, page: tableState.page});
                    break;
                default:
                    return;
            }
        },
        customSearchRender: (searchText, handleSearch, hideSearch, options) => {
            return (
                <CustomSearchRender
                    searchText={searchText}
                    onSearch={handleSearch}
                    onHide={hideSearch}
                    options={options}
                    loader={false}
                />
            );
        }
    };

    return (
        <>
            {/*Tabla*/}
            <WithLoader loader={loader}>
                <MUIDataTable
                    title={ (userId || personId) ? `HISTORIAL DE CRDITOS` : 'CRDITOS'}
                    data={credits}
                    columns={columns} options={options} />
            </WithLoader>

            <DlgListCredit ref={dialog} credit={credit}/>

        </>
    );
};


TableListCredit.propTypes = {
    personId: PropTypes.string,
    userId: PropTypes.number,
    searchable: PropTypes.bool,
};

TableListCredit.defaultProps = {
    personId: null,
    userId: null,
    searchable: true
};

export default TableListCredit;
