import React from 'react';
import PropTypes from 'prop-types';
import DlgConfirm from "../../_partials/_modals/DlgConfirm";
import useHttp from "../../../utils/useHttp";
import {toast} from "react-toastify";

const DlgPayOrMora = ({open, onClose, mora, selectPay, success}) => {
    const processPay = useHttp('put');

    // Marcar un pago como cobrado o como mora
    const confirmPayOrMora = () => {

        if (!selectPay) {
            toast.warn('Seleccione el pago a procesar');
            return;
        }

        let status = 2;
        if (mora)
            status = -1;
        processPay.request(`/payment/${selectPay}`, {status}).then(res => {
            if (res.ok) {
                toast.success(`Pago marcado como ${mora ? 'EN MORATORIA' : 'COBRADO'}`);
                success(selectPay, status);
                onClose();
            }
        }).catch(err => {
            toast.error(err);
        });
    };

    return (
        <div>
            {
                selectPay &&
                <DlgConfirm onConfirm={() => confirmPayOrMora()}
                            title={`${mora ? 'PONER EN MORA | ' : 'COBRAR | '} COD-${selectPay}`}
                            desc={`Estas seguro que deseas poner este pago en estado ${mora ? 'de MORA': 'COBRADO'}?`}
                            open={open}
                            loader={processPay.loader}
                            onClose={onClose} />
            }
        </div>
    );
};

DlgPayOrMora.propTypes = {
    mora: PropTypes.bool.isRequired,
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    selectPay: PropTypes.number,
    success: PropTypes.func
};

DlgPayOrMora.defaultProps = {
    mora: false
};

export default DlgPayOrMora;
