import React from 'react';
import {Container, Icon, Paper, Button} from "@material-ui/core";
import { withRouter } from 'react-router-dom'
import {ArrowBack} from '@material-ui/icons';

const Forbidden = ({history}) => {

    return (
        <Container maxWidth='sm' component={Paper} className='paper-content'>
            <div className={'forbidden-content'}>
                <h3>Acceso Denegado</h3>
                <Icon className={'forbidden-icon'}>security</Icon>
                <p>No tienes permisos para ver este contenido, si crees que es un error, contacta con el administrador.</p>
                <div>
                    <Button color={'primary'}
                            startIcon={<ArrowBack />}
                            variant={'contained'}
                            onClick={() => history.goBack(-1)}>Go Back</Button>
                </div>
            </div>
        </Container>
    );
};

export default withRouter(Forbidden);
