import React from 'react';
import {Alert} from "@material-ui/lab";

const StatusCredit = ({status, description}) => {


    if (status === 0) {
        return (
            <Alert severity="error">Este crdito fu anulado. Motivo: {description}</Alert>
        );
    }

    if (status === 1) {
        return (
            <Alert severity="info">Crdito actualmente activo</Alert>
        );
    }

    if (status === 2) {
        return (
            <Alert severity="success">Crdito finalizado</Alert>
        );
    }

    if (!status)
        return (<></>);
};

export default StatusCredit;
