import React, {useEffect, useState} from "react";
import {Grid} from "@material-ui/core";
import PropTypes from 'prop-types';
import PayItemCard from "./PayItemCard";
import DlgCancelPayment from "../_dialogs/DlgCancelPayment";
import DlgPayOrMora from "../_dialogs/DlgPayOrMora";
import PayMap from "./../_map/PayMap";

const PayCardOrMap = ({data, title, maps, grid}) => {
    const [dlgCancel, setDlgCancel] = useState(false);
    const [dlgPayMora, setDlgPayMora] = useState(false);
    const [isMora, setIsMora] = useState(false);
    const [selectPay, setSelectPay] = useState(null);
    const [localGrid, setLocalGrid] = useState({
       xs: 12, sm: 12, md: 12, lg: 6
    });

    const onExecute = (o, payload) => {
        if (!payload.id || !o) {
            return;
        }
        setSelectPay(payload.id);
        switch (o) {
            case 'cancel':
                setDlgCancel(true);
                //data[index].status = 1;
                break;
            case 'mora':
                setIsMora(true);
                setDlgPayMora(true);
                //data[index].status = -1;
                break;
            case 'pay':
                setIsMora(false);
                setDlgPayMora(true);
                //data[index].status = 2;
                break;
            default:
                break;
        }
    };

    const success = (paymentId, status) => {
        if (!paymentId || !status) return;
        let index = data.findIndex(d => d.id === paymentId);
        if (index >= 0) {
            data[index].status = status;
        }
    };

    useEffect(() => {
        const checkGrid = () => {
            if (grid.lg === 12 || grid.md === 12) {
                setLocalGrid({...grid, lg: 3});
            }
            if (grid.lg === 6 || grid.md === 6) {
                setLocalGrid({...grid, lg: 6, md: 12});
            }
        };
        checkGrid();
    }, [grid]);

    return (
        <>
            {
                data && data.length > 0
                    &&
                    <>
                        <h4>{title.toUpperCase()}</h4>
                        {/*------------ | Render Cards | ------------*/}
                        <Grid container spacing={2}>
                            {/*TARJETAS*/
                                !maps && data && data.map(p => (
                                    <Grid item xs={localGrid.xs} sm={localGrid.sm} md={localGrid.md} lg={localGrid.lg} key={p.id}>
                                        <PayItemCard pay={p} exec={(o, p) => onExecute(o, p)}/>
                                    </Grid>
                                ))
                            }
                            {/*MAPAS*/
                                maps && data &&
                                <Grid item xs={12}>
                                    <PayMap payments={data} height={'380px'} zoom={11} exec={(o, p) => onExecute(o, p)} />
                                </Grid>
                            }
                        </Grid>

                        {/*------------ | Dilogos | ------------*/}
                        <DlgCancelPayment open={dlgCancel}
                                          selectPay={selectPay}
                                          success={(p, s) => success(p, s)}
                                          onClose={() => setDlgCancel(false)} />

                        <DlgPayOrMora selectPay={selectPay}
                                      mora={isMora}
                                      open={dlgPayMora}
                                      success={(p, s) => success(p, s)}
                                      onClose={() => setDlgPayMora(false)} />
                    </>

            }

        </>
    );
};


PayCardOrMap.propTypes = {
    maps: PropTypes.bool.isRequired
};

PayCardOrMap.defaultProps = {
    maps: false
};

export default PayCardOrMap;
