import React, {useState} from 'react';
import MUIDataTable from "mui-datatables";
import MoneyOffIcon from '@material-ui/icons/MoneyOff';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import {Chip, Button, Tooltip, IconButton, Icon} from "@material-ui/core";
import useGet from "../../../utils/useGet";
import WithLoader from "../../_partials/hoc/WithLoader";
import {money, tableOptions} from "../../../config/config";
import {toDate} from "../../../utils/utils";
import DlgViewEmployUser from "../../employ/_dialog/DlgViewEmployUser";
import DlgCancelPayment from "../_dialogs/DlgCancelPayment";
import DlgPayOrMora from "../_dialogs/DlgPayOrMora";

const ViewPayments = ({creditId}) => {
    const pay = useGet(`/payment/only/${creditId}`);

    // dilogos
    const [open, setOpen] = useState(false);
    const [openInput, setOpenInput] = useState(false);
    const [openConfirm, setOpenConfirm] = useState(false);
    const [mora, setMora] = useState(false);

    // data seleccionada
    const [selectUser, setSelectUser] = useState(null);
    const [selectPay, setSelectPay] = useState(null);

    // columnas de la tabla
    const columns = [
        {
            name: 'total', label: 'TOTAL',
            options: {
                filter: false,
                customBodyRender: (value) => {return <b>{money(value)}</b>;}}
        },
        {
            name: 'date', label: 'F. PAGO',
            options: {
                filter: false,
                customBodyRender: (value) => {return toDate(value);}}
        },
        {
            name: 'description', label: 'DETALLE',
            options: {filter: false}
        },
        {
            name: 'status', label: 'ESTADO', options: {
                filter: true,
                customFilterListOptions: {render: v => `Estado: ${v}`},
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            {value === 2 &&
                            <>
                                <Chip
                                    size={'small'}
                                    label={"COBRADO"}
                                    className={'bg-green'}/>
                            </>
                            }
                            {value === 1 &&
                            <>
                                <Chip
                                    size={'small'}
                                    label="PENDIENTE"
                                    className={'bg-primary'}/>
                            </>
                            }
                            {value === -1 &&
                            <>
                                <Chip
                                    size={'small'}
                                    label="EN MORA"
                                    className={'bg-red'}/>
                            </>
                            }
                        </div>
                    );
                }
            }
        },
        {
            name: 'date_payment', label: 'F. COBRO',
            options: {
                filter: false,
                customBodyRender: (value) => {return toDate(value);}}
        },
        {
            name: 'mora', label: 'MORA',
            options: {
                customBodyRender: (value) => {return (value ? 'Si': 'No');}}
        },
        {
            name: 'user_id', label: 'EMPLEADO',
            options: {
                filter: false,
                customBodyRender: (value) => {return (
                    <>
                        { value && <Button size={'small'} onClick={() => showUser(value)}>Ver</Button> }
                        { !value && <Button size={'small'} disabled>Ver</Button>}
                    </>
                );}
            }
        },
        {
            name: "id",
            label: ' ',
            options: {
                customBodyRender: (value, tableMeta) => {
                    // status[-1: 'MORA', 1: 'PENDIENTE', 2: 'PROCESADO']
                    const status = tableMeta.rowData[3];
                    return <>
                        {
                            status === 1
                            &&

                                <Tooltip title={'Marcar pago como en MORA'}>
                                    <IconButton
                                        onClick={() => setPay(value, true, true)}
                                        size={"small"}
                                        aria-label="Poner en moratoria el pago">
                                        <MoneyOffIcon/>
                                    </IconButton>
                                </Tooltip>
                        }
                        {
                            (status === 1 || status === -1) &&
                            <Tooltip title={'Marcar pago como COBRADO'}>
                                <IconButton
                                    onClick={() => setPay(value, true, false)}
                                    size={"small"}
                                    aria-label="Cobrar este pago">
                                    <MonetizationOnIcon/>
                                </IconButton>
                            </Tooltip>
                        }
                        {
                            status === 2
                            &&
                            <Tooltip title={'Anular'}>
                                <IconButton
                                    onClick={() => setPay(value)}
                                    size={"small"}
                                    aria-label="Anular Pago">
                                    <Icon>cancel</Icon>
                                </IconButton>
                            </Tooltip>
                        }
                    </>;
                }
            }
        },
    ];

    // mostrar datos del empleado
    const showUser = (userId) => {
        setSelectUser(userId);
        setOpen(true);
    };

    // abrir el dialogo de ingreso de motivo
    const setPay = (paymentId, confirm = false, mora = false) => {
        if (confirm) {
            setOpenConfirm(true);
            setMora(mora);
        }
        else {
            setOpenInput(true);
        }
        setSelectPay(paymentId);
    };

    const options = {
        ...tableOptions,
        electableRowsHeader: false,
        filter: false,
        search: false,
        sort: false,
        download: false,
        print: false,
        viewColumns: false,
        selectableRows: 'none',
        setTableProps: () => {
            return {
                padding: "default" ,
                size: "small"
            };
        }
    };

    return (
        <WithLoader loader={pay.loader} elevation={0} text={''}>
            <MUIDataTable
                title={'Pagos'}
                data={pay.data.pays}
                columns={columns} options={options} />
            {
                selectUser && <DlgViewEmployUser userId={selectUser}
                                                 onClose={() => setOpen(false)} open={open} />
            }


            <DlgCancelPayment selectPay={selectPay}
                               open={openInput}
                               onClose={() => setOpenInput(false)}/>

            <DlgPayOrMora selectPay={selectPay}
                          mora={mora}
                          open={openConfirm}
                          onClose={() => setOpenConfirm(false)}/>
        </WithLoader>
    );
};

export default ViewPayments;

