import React from "react";
import PropTypes from 'prop-types';
import {money} from "../../../config/config";
import {Link} from "react-router-dom";
import {CREDIT_BY_CLIENT, toName} from "../../../config/urls";
import BtnsPayOptions from "./BtnsPayOptions";

const PayItemCard = ({pay, exec}) => {

    const color = (status) => {
        switch (status) {
            case 1: return 'pendiente';
            case 2: return 'cobrado';
            case -1: return 'mora';
            default: return 'pendiente';
        }
    };

    return (
        <div className={`pay-content ${color(pay.status)}`}>
            <div className={'pay-header'}>
                <div className={`dollar ${color(pay.status)}`}>{money(pay.total)}</div>
                <small>Pago #{pay.number}</small>
            </div>
            <div className={'client'}>
                <Link to={toName(CREDIT_BY_CLIENT, {id: pay.client_id})} className={'client'}>{pay.client_name} {pay.client_surname}</Link>
                <small className={color(pay.status)}>{color(pay.status).toUpperCase()}</small>
            </div>
            <div className={'buttons'}>
                <BtnsPayOptions exec={exec} pay={pay} />
            </div>
        </div>
    );
};

PayItemCard.propTypes = {
    exec: PropTypes.func
};
export default PayItemCard;
