import React from 'react';
import useGet from "../../../utils/useGet";
import {Grid, Icon} from '@material-ui/core';
import {Skeleton} from "@material-ui/lab";
import {money} from "../../../config/config";
const BoxInfo = ({info, text, icon, color}) => {
    return (
        <div className={`box-info  ${(color) ? color.toLowerCase() : ''}`}>
            {!icon ? <Icon>today</Icon> : <Icon>{icon}</Icon>}

            <h3>{info}</h3>
            <p>{text}</p>
        </div>);
};

const ExpenseInfo = () => {
    const info = useGet('/expense/info');
    return (
        <div style={{marginBottom: '1.5rem', padding: '0 10px'}}>
            <Grid container spacing={3}>

                {
                    info.loader &&
                    <>
                        <Grid item xs={12} sm={4}>
                            <Skeleton variant="rect" height={79} />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Skeleton variant="rect" height={79}/>
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Skeleton variant="rect" height={79}/>
                        </Grid>
                    </>
                }
                {
                    !info.loader && info.data &&
                    <>
                        <Grid item xs={12} sm={4}>
                            <BoxInfo text={'GASTOS HOY'}
                                     info={money(info.data.today)} />
                        </Grid>
                        <Grid item xs={12} sm={4} >
                            <BoxInfo text={'GASTOS ESTA SEMANA'}
                                     color={'cyan'}
                                     info={money(info.data.week)} icon={'attach_money'} />
                        </Grid>
                        <Grid item xs={12} sm={4} >
                            <BoxInfo text={'GASTOS ESTE MES'}
                                     color={'purple'}
                                     info={money(info.data.month)} icon={'event'}/>
                        </Grid>
                    </>
                }
            </Grid>
        </div>
    );
};

export default ExpenseInfo;
