import React, {forwardRef, useImperativeHandle, useState} from 'react';
import DialogViewMapLoc from "../../maps/_dialog/DialogViewMapLoc";
import DlgInput from "../../_partials/_modals/DlgInput";
import DlgConfirm from "../../_partials/_modals/DlgConfirm";
import {toast} from "react-toastify";
import useHttp from "../../../utils/useHttp";

const DlgListCredit = forwardRef(({credit}, ref) => {
    const reqUpdate = useHttp('put');

    // Dialogs
    const [dialogMap, setDialogMap] = useState(false);
    const [dialogInput, setDialogInput] = useState(false);
    const [dialogConfirm, setDialogConfirm] = useState(false);

    // Anular crdito
    const cancelCredit = (motivo) => {
        if (!motivo) {
            toast.warn('Es necesario especificar el motivo de la anulacin');
            return;
        }
        reqUpdate.request(`/credit/cancel/${credit}`, {description: motivo}).then(res => {
            if (res.ok) {
                setDialogInput(false);
                toast.success('Crdito anulado con xito');
            }
        }).catch(err => {
            toast.error(err);
        })
    };

    // finalizar crdito
    const finishCredit = () => {
        if (!credit) {
            toast.error('Vuelve a seleccionar el crdito que deseas finalizar');
            return;
        }

        reqUpdate.request(`/credit/end/${credit}`).then(res => {
            if (res.ok) {
                toast.success('El crdito fu finalizado con xito');
                setDialogConfirm(false);
            }
        }).catch(err => toast.error(err));
    };


    useImperativeHandle(ref, () => ({

        openConfirm() {
            setDialogConfirm(true);
        },

        openInput() {
            setDialogInput(true);
        },

        openMap() {
            setDialogMap(true);
        }

    }));

    return (
        <>
            {/*Ver la localizacin*/
                credit && credit.geo_lon && credit.geo_lat &&
                <DialogViewMapLoc
                    title={`${credit.client.toUpperCase()}`}
                    description={`${credit.address}  ${ (credit.ref_detail) ? ' - '+credit.ref_detail : '' }`}
                    isOpen={dialogMap}
                    onClose={() => setDialogMap(false)}
                    lon={parseFloat(credit.geo_lon)} lat={parseFloat(credit.geo_lat)} />
            }
            {/*------------ | CONFIRMAR ANULACIN | ------------*/
                credit &&
                <DlgInput title={`Anular crdito COD-${credit}`}
                          desc={`Ests seguro de que deseas anular el crdito COD-${credit}?`}
                          onClose={() => setDialogInput(false)}
                          open={dialogInput}
                          onConfirm={(e) => cancelCredit(e)}/>
            }
            {/*------------ | CONFIRMAR FINALIZACIN | ------------*/
                credit &&
                <DlgConfirm title={`Finalizar crdito COD-${credit}`}
                            desc={`Al finalizar un crdito el sistema entiende que se cumpli con todos los pagos y todo es correcto Desea finalizar?`}
                            onConfirm={() => finishCredit()}
                            open={dialogConfirm}
                            onClose={() => setDialogConfirm(false)}/>
            }
        </>
    );
});

export default DlgListCredit;
