import React from 'react';
import {Button, Dialog, DialogActions, DialogContent, useMediaQuery, useTheme} from "@material-ui/core";
import PropTypes from 'prop-types';
import {asset} from "../../../config/config";

const DlgViewImg = ({isOpen, onClose, img, description, title}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

    return (
        <Dialog
            fullScreen={fullScreen}
            open={isOpen}
            maxWidth={'md'}
            onClose={() => onClose()}
            aria-labelledby="responsive-dialog-title">
            <DialogContent className={'map-dialog'}>
                <div className={'dialog-map-header'}>
                    <h2>{title}</h2>
                    {
                        description && <small className={'text-muted'}>{description}</small>
                    }
                </div>
                <div>
                    <img src={asset(img)} alt={img} width={'100%'}/>
                </div>
            </DialogContent>
            <DialogActions>
                <Button autoFocus onClick={() => onClose()} color="primary">
                    OK
                </Button>
            </DialogActions>
        </Dialog>
    );
};

DlgViewImg.propTypes = {
    img: PropTypes.string.isRequired,
    onClose: PropTypes.func,
    title: PropTypes.string
};


DlgViewImg.defaultProps = {
    img: null,
    isOpen: false,
    description: '',
    title: 'Ver Imagen'
};

export default DlgViewImg;
