import React, {useState} from 'react';
import {
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Button,
    TextField,
    useTheme, useMediaQuery
} from "@material-ui/core";
import PropTypes from 'prop-types';

const DlgInput = ({open, onClose, onConfirm, title, desc, inputLabel, loader}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));
    const [text, setText] = useState('');
    return (
        <Dialog open={open}
                onEnter={() => setText('')}
                onClose={onClose} aria-labelledby="form-dialog-title" fullScreen={fullScreen}>
            <DialogTitle id="form-dialog-title">{title}</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    {desc}
                </DialogContentText>
                <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    multiline={true}
                    rows={2}
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                    label={inputLabel}
                    type="text"
                    fullWidth
                />
            </DialogContent>
            <DialogActions>
                <Button color={'primary'}  onClick={onClose}>
                    Cancelar
                </Button>
                <Button color="secondary" onClick={() => onConfirm(text)} disabled={loader}>
                    { loader ? 'Espere...' : 'Anular'}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

DlgInput.propTypes = {
    onClose: PropTypes.func.isRequired,
    onConfirm: PropTypes.func.isRequired,
    open: PropTypes.bool.isRequired,
    onFieldChange: PropTypes.func,
    title: PropTypes.string,
    desc: PropTypes.string,
    inputLabel: PropTypes.string,
    loader: PropTypes.bool
};

DlgInput.defaultProps = {
    title: 'Confirmar',
    desc: 'Ingrese el motivo de la anulacin',
    inputLabel: 'Motivo de la anulacin',
    loader: false,
};

export default DlgInput;
