import React, {useRef, useState, Component} from 'react';
import PropTypes from 'prop-types';
import Master from "../Master";
import {
    Container, Paper, Grid, Button, Icon, IconButton,
} from "@material-ui/core";
import {Settings} from '@material-ui/icons';
import WithLoader from "../_partials/hoc/WithLoader";
import SelectZone from "../zone/_partials/SelectZone";
import SelectCobro from "./_partials/SelectCobro";
import SelectDate from "./_partials/SelectDate";
import Axios from "axios";
import {toast} from "react-toastify";
import {processErrors} from "../../utils/utils";
import PayCardOrMap from "./_partials/PayCardOrMap";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";

const INITIAL = {
    payments: [],
    loader: false,
    map: (localStorage.getItem('map') === 'true'),
    grid: {md: 6, sm: 12, xs: 12},
    filters: {
        date: null,
        zone: null,
        cobro: null
    }
};

class PaymentList extends Component {

    constructor(props) {
        super(props);
        this.state = INITIAL
    }

    componentDidMount() {
        this.getPays();
    }

    getPays = (filters = INITIAL.filters) => {
        let url = '/payment?q=';

        if (filters.date) {
            url += `&date=${filters.date}`;
        }
        if (filters.zone) {
            url += `&zone=${filters.zone}`;
        }
        if (filters.cobro) {
            url += `&only=${filters.cobro}`;
        }
        this.setState({...this.state, filters: filters, loader: true});
        Axios.get(url).then(res => {
            if (res.ok) {

                let i = 0;
                let grid = INITIAL.grid;
                if (res.data.diario && res.data.diario.length > 0) i++;
                if (res.data.semanal && res.data.semanal.length >0 ) i++;
                if (res.data.quincenal && res.data.quincenal.length >0) i++;
                if (res.data.mensual && res.data.mensual.length >0) i++;

                if ( (i === 1 || i === 2) && this.state.map ) {
                    grid = {
                        xs: 12,
                        sm: 12,
                        md: 12,
                        lg: 12
                    }
                }
                if (i===1 && !this.state.map) {
                    grid = {
                        xs: 12,
                        sm: 12,
                        md: 12,
                        lg: 12
                    }
                }

                this.setState({...this.state, payments: res.data, grid: grid});
            }
        }).catch(err => toast.error(processErrors(err)))
            .finally(() => this.setState({...this.state, loader: false}));

    };

    onConfig = type => {
        switch (type) {
            case 'map':
                this.setState({...this.state, map: true});
                localStorage.setItem('map', 'true');
                break;
            case 'noMap':
                this.setState({...this.state, map: false});
                localStorage.setItem('map', 'false');
                break;
            case 'x1':
                this.setState({...this.state, grid: {...this.state.grid, md:12}});
                break;
            case 'x4':
                this.setState({...this.state, grid: {...this.state.grid, md:6}});
                break;
            default:
                break;
        }
    };

    onFilters = filters => {
        this.getPays(filters);
    };

    render() {
        return (
            <Master>
                <Container component={Paper}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <PayFilters initFilters={this.state.filters}
                                        onFilter={(f) => this.onFilters(f)}>
                                <PaymentListConfig map={this.state.map} onChange={(e) => this.onConfig(e)}/>
                            </PayFilters>
                        </Grid>
                    </Grid>
                    <WithLoader loader={this.state.loader} elevation={0}>
                        <Grid container spacing={2}>
                            <Grid item xs={this.state.grid.xs} sm={this.state.grid.sm} md={this.state.grid.md}>
                                <PayCardOrMap title={'Diario'}
                                              grid={this.state.grid}
                                              maps={this.state.map}
                                              data={this.state.payments.diario}/>
                            </Grid>
                            <Grid item xs={this.state.grid.xs} sm={this.state.grid.sm} md={this.state.grid.md}>
                                <PayCardOrMap title={'Semanal'}
                                              grid={this.state.grid}
                                              maps={this.state.map}
                                              data={this.state.payments.semanal}/>
                            </Grid>

                            <Grid item xs={this.state.grid.xs} sm={this.state.grid.sm} md={this.state.grid.md}>
                                <PayCardOrMap title={'Quincenal'}
                                              maps={this.state.map}
                                              grid={this.state.grid}
                                              data={this.state.payments.quincenal}/>
                            </Grid>

                            <Grid item xs={this.state.grid.xs} sm={this.state.grid.sm} md={this.state.grid.md}>
                                <PayCardOrMap title={'Mensual'}
                                              maps={this.state.map}
                                              grid={this.state.grid}
                                              data={this.state.payments.mensual}/>
                            </Grid>
                        </Grid>
                    </WithLoader>
                </Container>
            </Master>
        );
    }
}

const PaymentListConfig = ({map, onChange}) => {
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = (type = '') => {
        onChange(type);
        setAnchorEl(null);
    };

    return (
        <div>
            <IconButton aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
                <Settings />
            </IconButton>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                {
                    !map ? <MenuItem onClick={() => handleClose('map') }>Vista de Mapa</MenuItem>
                        : <MenuItem onClick={() => handleClose('noMap') }>Vista de Pagos</MenuItem>
                }

                <MenuItem onClick={() => handleClose('x1')}>Cuadricula x1</MenuItem>
                <MenuItem onClick={() => handleClose('x4')}>Cuadricula x4</MenuItem>
            </Menu>
        </div>
    );
};

const PayFilters = ({onFilter, onClear, initFilters, children}) => {
    const selectZRef = useRef();
    const selectCRef = useRef();
    const selectDRef = useRef();

    const [filters, setFilters] = useState(initFilters);

    const clearFilters = () => {
        setFilters({date: null, zone: null, cobro: null});
        selectZRef.current.reset();
        selectCRef.current.reset();
        selectDRef.current.reset();
    };

    return (
      <Grid container spacing={1}>
          <Grid item xs={6} md={3}>
              <SelectZone ref={selectZRef}
                          onChange={(z) => setFilters({...filters, zone: z})} />
          </Grid>
          <Grid item xs={6} md={3}>
              <SelectCobro ref={selectCRef}
                           onChange={(c) => setFilters({...filters, cobro: c})} />
          </Grid>
          <Grid item xs={12} md={3}>
            <SelectDate label={'Fecha recaudacin'}
                        initialDate={filters.date}
                        ref={selectDRef}
                        onChange={(d) => setFilters({...filters, date: d})}/>
          </Grid>
          <Grid item xs={12} sm={12} md={3} className={'d-flex'}>
              {/*------------ | Botones | ------------*/}
              <Button variant={'text'} color={'primary'} fullWidth onClick={() => onFilter(filters)}>
                  <Icon>filter_list</Icon> &nbsp; Filtrar
              </Button>
              <IconButton onClick={() =>clearFilters()}>
                  <Icon>clear_two_tone</Icon>
              </IconButton>
              {children}
          </Grid>
      </Grid>
    );
};

PayFilters.propTypes = {
    onFilter: PropTypes.func.isRequired,
    onClear: PropTypes.func
};
PayFilters.defaultProps = {
    filters: {
        date: null,
        zone: null,
        cobro: null
    }
};

export default PaymentList;


