import React from 'react';
import {Grid, Paper, TextField} from "@material-ui/core";

const ClientDataCredit = ({name, surname, address, clientId}) => {

    const getName = () => {
        if (!name && !surname) return 'No especifica';
        if (name && !surname) return name.toUpperCase();
        if (name && surname) return `${name.toUpperCase()} ${surname.toUpperCase()}`;
        return 'No especifica';
    };

    return (
        <Paper className={'p-1'} style={{borderRadius: 0}}>
            <div className={'title-muted'}>Datos del Cliente</div>
            <Grid container className={'mt-1'} spacing={1}>
                <Grid item xs={6}>
                    <TextField readOnly label={'Nombres'} multiline={true} value={getName()} fullWidth />
                </Grid>
                <Grid item xs={6}>
                    <TextField readOnly label={'Direccin'} multiline={true} value={address ? address : 'Ninguna'} fullWidth />
                </Grid>
            </Grid>
        </Paper>
    );
};

export default ClientDataCredit;
