import React, {useState} from 'react';
import {
    Button,
    Checkbox,
    FormControl,
    FormControlLabel,
    Grid,
    InputLabel,
    LinearProgress, MenuItem,
    Select,
    TextField, Divider
} from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import {KeyboardDatePicker} from "@material-ui/pickers";
import Alert from "../../_partials/Alert";
import useHttp from "../../../utils/useHttp";
import {toast} from "react-toastify";
import moment from "moment";
import DetailEmploy from "../DetailEmploy";

const FrmPayRoll = ({ employs, employ }) => {

    const [data, setData] = useState({
        employ_id:      (employ) ? employ.id : null,
        amount:         (employ) ? employ.sueldo: 0,
        selectedDate:   moment(),
        extra:          0,
        bonus:          0,
        discount:       0,
        date:           moment().format('YYYY-MM-DD'),
        description:    '',
        advance:        false,
        concept:        'PAGO DE SUELDO'
    });

    const [err, setErr] = useState(null);

    const httpStore = useHttp();

    const onSubmit = (e) => {
      e.preventDefault();
      setErr(null);

      httpStore.request('/payroll', data).then(res => {
          if(res.ok) {
              toast.success('Pago de Sueldo exitoso');
          }
      }).catch(err => setErr(err))
    };

    const onChangeEmploy = (option, value) => {
        setData({...data, employ_id: null})
        if (value!==null){

            setData({
                ...data,
                employ_id: value.id,
                amount: value.sueldo
            });

        }
        else{
            setData({...data, employ_id: null})
        }
    };

    const onChangeDate = date => {
        setData({
            ...data,
            selectedDate: date,
            date: moment(date).format('YYYY-MM-DD')
        });
    };

    const onChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value});
    };

    return (
        <>
            <form onSubmit={e => onSubmit(e)}>
            <Grid container spacing={3}>
                {/*Nombre*/}
                <Grid item xs={12} sm={6}>
                    {
                        employs &&
                            <Autocomplete
                                id="employ_id"
                                options={employs.data}
                                onChange={(option, value) => onChangeEmploy(option, value)}
                                getOptionLabel={option => (option.name+' '+option.surname).toUpperCase() }
                                renderInput={params => (
                                    <TextField
                                        {...params}
                                        required
                                        label="Empleado" variant="standard" fullWidth />
                                )}
                            />
                    }
                    {
                        employs === null && employ.name &&
                            <FormControl fullWidth>
                                <InputLabel id="employ_id_2">Empleado</InputLabel>
                                <Select
                                    labelId="employ_id_2"
                                    name={'employ_id'}
                                    value={data.employ_id}
                                    fullWidth >
                                    <MenuItem value={employ.id}>{employ.name + ' ' + employ.surname}</MenuItem>
                                </Select>
                            </FormControl>
                    }

                </Grid>
                {/*Date*/}
                <Grid item xs={12} sm={6}>
                    <KeyboardDatePicker
                        disableToolbar
                        variant="inline"
                        format="DD/MM/YYYY"
                        fullWidth
                        autoOk={true}
                        id="date"
                        label="Fecha de pago"
                        value={data.selectedDate}
                        onChange={(date) => onChangeDate(date)}
                        KeyboardButtonProps={{
                            'aria-label': 'Cambiar Fecha',
                        }}
                    />
                </Grid>
                {/*Monto*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='number' label='Monto $'
                               name='amount'
                               onChange={e => onChange(e)}
                               value={data.amount}
                               required fullWidth/>
                </Grid>
                {/*Bonus*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='number' label='+ Comisin $'
                               name='bonus'
                               onChange={e => onChange(e)}
                               value={data.bonus}
                               fullWidth/>
                </Grid>
                {/*Extra*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='number' label='+ Valor Adicional $'
                               name='extra'
                               onChange={e => onChange(e)}
                               value={data.extra}
                               fullWidth/>
                </Grid>
                {/*Descuento*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='number' label='- Descuento $'
                               name='discount'
                               onChange={e => onChange(e)}
                               value={data.discount}
                               fullWidth/>
                </Grid>
                {/*Description*/}
                <Grid item xs={12} sm={6}>
                    <TextField
                        id="description"
                        label="Detalle | Observacin"
                        multiline
                        rows="4"
                        fullWidth
                        name='description'
                        onChange={e => onChange(e)}
                        value={data.description}
                    />
                </Grid>
                {/*Adelnto y Concepto*/}
                <Grid item xs={12} sm={6}>
                    <FormControlLabel
                        control={
                            <Checkbox checked={data.advance}
                                      onChange={e => setData({
                                          ...data,
                                          advance: !data.advance,
                                            concept: (!data.advance) ? 'ADELANTO DE SUELDO' : 'PAGO DE SUELDO'
                                      })}
                                      value="advance" />
                        }
                        label="ADELANTO"
                    />
                    <TextField
                        id="concept"
                        label="Concepto"
                        multiline
                        disabled
                        rows="2"
                        fullWidth
                        name='concept'
                        value={data.concept}
                    />
                </Grid>
                {/*Submit*/}
                <Grid item xs={12} className='center'>
                    <Alert show={err!==null} message={err} />
                    { httpStore.loader && <LinearProgress color="secondary" />}
                    <br/>
                    <Button type='submit' color='primary'
                            disabled={httpStore.loader}
                            variant='contained'>
                        Pagar Sueldo
                    </Button>
                </Grid>
            </Grid>
        </form>
            {
                data.employ_id !== null && <>
                    <br/>
                    <Divider /> <br />
                    <DetailEmploy employ={data.employ_id} /></>
            }

        </>
    );
};

FrmPayRoll.defaultProps = {
  employs: null,
  employ: null
};

export default FrmPayRoll;
