import React from 'react';
import Master from "../Master";
import useGet from "../../utils/useGet";
import {Link} from "react-router-dom";
import WithLoader from "../_partials/hoc/WithLoader";
import {EMPLOY_EDIT, PAYROLL_ADD_USER, toName} from "../../config/urls";
import MUIDataTable from "mui-datatables";
import {Icon, IconButton, Chip} from "@material-ui/core";
import {ArrowUpward, ArrowDownward} from '@material-ui/icons'
import {tableOptions} from "../../config/config";
import useHttp from "../../utils/useHttp";
import {toast} from "react-toastify";
import WithAdminContent from "../_partials/hoc/WithAdminContent";

const ListEmploy = () => {

    const employs = useGet('/employ');
    const status = useHttp('put');
    const remove = useHttp('delete');

    const columns = [
        {name: "id", label: 'ID', options: {display: 'false', filter: false}},
        {
            name: 'name', label: 'NOMBRE',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}}
        },
        {
            name: 'surname', label: 'APELLIDO',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}}
        },
        {
            name: 'username', label: 'USUARIO', options: {filter: false}
        },
        {
            name: 'phones', label: 'TELFONO', options: {filter: false}
        },
        {
            name: 'sueldo', label: 'SUELDO', options: {filter: false}
        },
        {
            name: 'status', label: 'ESTADO', options: {
                filter: true,
                customFilterListOptions: { render: v => `Estado: ${v}` },

                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            { value === 1 &&
                                <>
                                    <Chip
                                        label={"Activo"}
                                        className={'bg-green'}/>
                                    <IconButton size={"small"}
                                                onClick={() => changeStatus(tableMeta.rowData[0], updateValue)}
                                                aria-label="Dar de baja">
                                        <ArrowDownward/>
                                    </IconButton>
                                </>
                            }
                            { value === 0 &&
                                <>
                                    <Chip
                                        label="Inactivo"
                                        className={'bg-red'}/>
                                    <IconButton size={"small"}
                                                onClick={() => changeStatus(tableMeta.rowData[0], updateValue)}
                                                aria-label="Dar de alta">
                                        <ArrowUpward/>
                                    </IconButton>
                                </>
                            }
                        </div>
                    );
                }
            }
        },
        {
            label: 'OPCIONES', name: 'id',
            options: {
                filter: false,
                sort: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            <IconButton component={Link}
                                        to={toName(EMPLOY_EDIT, {id: value})}
                                        size={"small"}
                                        aria-label="Editar">
                                <Icon>edit</Icon>
                            </IconButton>
                            <IconButton onClick={() => deleteEmploy(value, updateValue)}
                                        size={"small"}
                                        aria-label="delete">
                                <Icon>delete</Icon>
                            </IconButton>
                            <IconButton component={Link}
                                        to={toName(PAYROLL_ADD_USER, {id: value})}
                                        size={"small"}
                                        aria-label="Pagar">
                                <Icon>payment</Icon>
                            </IconButton>
                        </div>
                    );
                },
            }
        }
    ];

    const changeStatus = (employId, updateValue) => {

        status.request(`/employ/cancel/${employId}`).then(res => {
            if(res.ok) {
                let index = employs.data.findIndex(d => d.id === employId);

                const status = employs.data[index].status;
                if (status === 1)
                   employs.data[index].status = 0;
                else
                   employs.data[index].status = 1;

                updateValue(employs.data[index].status);

                toast.success(res.message);
            }
        }).catch(err => {
            toast.error(err);
        });
    };

    const deleteEmploy = (employId) => {
        if(!window.confirm('Esta seguro que desea eliminar este empleado?')) return;

        remove.request(`/employ/${employId}`).then(res => {
            if(res.ok){
                let index = employs.data.findIndex(d => d.id === employId);
                employs.data.splice(index, 1);

                toast.success(res.message);
                const data = employs.data;
                employs.setData();
                employs.setData(data);

            }
        }).catch(err => toast.error(err));
    };

    return (
        <Master>
            <WithLoader loader={employs.loader}>
                <WithAdminContent>
                    <MUIDataTable
                        title={"EMPLEADOS"}
                        data={employs.data}
                        columns={columns}
                        options={tableOptions}
                    />
                </WithAdminContent>
            </WithLoader>
        </Master>
    );
};

export default ListEmploy;
