import React from 'react';
import {Grid, Icon} from "@material-ui/core";

const BoxInfoCredit = ({info, text, icon, color, md=2}) => {
    return (
        <Grid item xs={6} sm={4} md={md}>
            <div className={`box-info  ${(color) ? color.toLowerCase() : ''}`} style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}}>
                {!icon ? <Icon>today</Icon> : <Icon>{icon}</Icon>}

                <h3>{info}</h3>
                <p>{text}</p>
            </div>
        </Grid>
    );
};

export default BoxInfoCredit;
