import React from 'react';
import Master from "../Master";
import {Container, Divider, Grid, Icon, Paper} from "@material-ui/core";
import FrmPayRoll from "./_form/FrmPayRoll";
import useGet from "../../utils/useGet";
import WithLoader from "../_partials/hoc/WithLoader";
import WithAdmin from "../_partials/hoc/WithAdmin";

const RegisterPayRollUser = ({match}) => {

    const id = match.params.id;
    const employ = useGet(`/employ/${id}`);

    return (
        <Master>
            <WithAdmin>
                <WithLoader loader={employ.loader}>
                    <Container maxWidth='lg' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>payment</Icon> Pagar sueldo</h4>
                            <Divider />
                        </div>
                        <Grid container>

                                    <>
                                        {
                                            employ &&
                                            <Grid item xs={12}>
                                                <FrmPayRoll employ={employ.data}/>
                                            </Grid>
                                        }
                                    </>
                        </Grid>
                    </Container>
                </WithLoader>
            </WithAdmin>
        </Master>
    );
};

export default RegisterPayRollUser;
