import React from 'react';
import {Collapse, Divider, Icon, List, ListItem, ListItemIcon, ListItemText} from "@material-ui/core";
import {NavLink, withRouter} from "react-router-dom";
import {
    CLIENT_LIST, CREDIT_LIST,
    EMPLOY_ADD,
    EMPLOY_LIST,
    EXPENSE_LIST, PAYMENT_LIST,
    PAYROLL_ADD,
    PAYROLL_LIST,
    ZONE_LIST
} from "../../config/urls";
import WithAdmin from "./hoc/WithAdmin";
import {AttachMoney, ExpandLess, ExpandMore, PersonAdd, LocalAtmRounded, LocalAtm} from "@material-ui/icons";

class DrawerCustom extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            openSubmenu: {
                employ: false,
                payroll: false,
                client: false
            }
        };
    }

    componentDidMount() {
        if(this.props.history.location.pathname.toString().includes(EMPLOY_LIST) && !this.state.openSubmenu.employ){
            this.openSubmenuNow('employ');
        }

        if(this.props.history.location.pathname.toString().includes(PAYROLL_LIST) && !this.state.openSubmenu.payroll){
            this.openSubmenuNow('payroll');
        }
    }

    openSubmenuNow (submenu) {
        this.setState({
            openSubmenu: {
                [submenu]: !this.state.openSubmenu[submenu]
            }
        });
    };

    render() {
        return (
            <>
                <div className={this.props.classes.toolbar}>
                    <div className="toolbar-custom">
                        <img src={"/img/payicon.png"} alt="PayCenter" height={40}/>
                        <h5>AdminDashboard</h5>
                    </div>
                </div>
                <Divider />
                <List>

                    <ListItem button activeClassName={'active'} component={NavLink} to={PAYMENT_LIST} >
                        <ListItemIcon><LocalAtm/></ListItemIcon>
                        <ListItemText primary='Cobros' />
                    </ListItem>

                    <ListItem button activeClassName={'active'} component={NavLink} to={CREDIT_LIST} >
                        <ListItemIcon><Icon>monetization_on</Icon></ListItemIcon>
                        <ListItemText primary='Crditos' />
                    </ListItem>

                    {/*Zonas*/}

                    <ListItem button activeClassName={'active'} component={NavLink} to={ZONE_LIST} >
                        <ListItemIcon><Icon>map</Icon></ListItemIcon>
                        <ListItemText primary='Rutas/Zonas' />
                    </ListItem>

                    {/*Clientes*/}

                    <ListItem button activeClassName={'active'} component={NavLink} to={CLIENT_LIST} >
                        <ListItemIcon><Icon>people_alt</Icon></ListItemIcon>
                        <ListItemText primary='Clientes' />
                    </ListItem>



                    {/*Admin Routes*/}
                    <WithAdmin>
                        {/*Empleados*/}
                        <>
                            <ListItem button onClick={ () => this.openSubmenuNow('employ')}>
                                <ListItemIcon><Icon>group</Icon></ListItemIcon>
                                <ListItemText primary='Empleados' />
                                {this.state.openSubmenu.employ ? <ExpandLess /> : <ExpandMore />}
                            </ListItem>

                            <Collapse in={this.state.openSubmenu.employ} timeout="auto" unmountOnExit>
                                <List component="div" disablePadding>
                                    {/*Agregar Empleados*/}
                                    <ListItem button className={this.props.classes.nested} activeClassName={'active'}
                                              exact
                                              component={NavLink} to={EMPLOY_LIST}>
                                        <ListItemIcon>
                                            <Icon>list</Icon>
                                        </ListItemIcon>
                                        <ListItemText primary="Listar" />
                                    </ListItem>

                                    <ListItem button className={this.props.classes.nested} activeClassName={'active'}
                                              exact
                                              component={NavLink} to={EMPLOY_ADD}>
                                        <ListItemIcon>
                                            <PersonAdd />
                                        </ListItemIcon>
                                        <ListItemText primary="Agregar" />
                                    </ListItem>
                                </List>
                            </Collapse>
                        </>

                        {/*Roles de Pago*/}
                        <>
                            <ListItem button onClick={ () => this.openSubmenuNow('payroll')}>
                                <ListItemIcon><AttachMoney/></ListItemIcon>
                                <ListItemText primary='Sueldo/Salario' />
                                {this.state.openSubmenu.payroll ? <ExpandLess /> : <ExpandMore />}
                            </ListItem>

                            <Collapse in={this.state.openSubmenu.payroll} timeout="auto" unmountOnExit>
                                <List component="div" disablePadding>
                                    {/*Agregar Empleados*/}
                                    <ListItem button className={this.props.classes.nested} activeClassName={'active'}
                                              exact
                                              component={NavLink} to={PAYROLL_LIST}>
                                        <ListItemIcon>
                                            <Icon>list</Icon>
                                        </ListItemIcon>
                                        <ListItemText primary="Listar" />
                                    </ListItem>

                                    <ListItem button className={this.props.classes.nested} activeClassName={'active'}
                                              exact
                                              component={NavLink} to={PAYROLL_ADD}>
                                        <ListItemIcon>
                                            <LocalAtmRounded />
                                        </ListItemIcon>
                                        <ListItemText primary="Pagar" />
                                    </ListItem>
                                </List>
                            </Collapse>
                        </>
                    </WithAdmin>

                    {/*Gastos*/}
                    <ListItem button activeClassName={'active'} component={NavLink} to={EXPENSE_LIST} >
                        <ListItemIcon><Icon>local_atm</Icon></ListItemIcon>
                        <ListItemText primary='Gastos' />
                    </ListItem>
                </List>
            </>
        );
    }


};

export default withRouter(DrawerCustom);
