import React, {useState} from 'react';
import {
    Grid,
    InputLabel,
    Select,
    TextField,
    MenuItem,
    InputAdornment,
    IconButton,
    Input,
    FormControl, LinearProgress, Button, FormControlLabel, Checkbox, FormHelperText
} from "@material-ui/core";
import {withRouter} from 'react-router-dom';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import Alert from "../../_partials/Alert";
import useHttp from "../../../utils/useHttp";
import {toast} from "react-toastify";
import useGet from "../../../utils/useGet";
import WithLoader from "../../_partials/hoc/WithLoader";
import {EMPLOY_LIST} from "../../../config/urls";


const FrmEmploy = ({method, url, initialData, initialRoutes, history}) => {

    const [data, setData] = useState(initialData);
    const [values, setValues] = useState({
        showPassword: false,
        err: null,
        checked: {},
    });
    const [routes, setRoutes] = useState(initialRoutes);

    const zones = useGet('/route');
    const http = useHttp(method);

    const onSubmit = (e) => {
        e.preventDefault();
        setValues({
            ...values,
            err: null
        });

        http.request(url, data).then(res => {
            if(res.ok && method==='post') {
                toast.success('Empleado Guardado con xito');
                setValues({
                    ...values, checked: {}
                });
                history.push(EMPLOY_LIST);
            }

            if(res.ok && method==='put') {
                toast.success(res.message);
            }
        }).catch(err =>{
            setValues({
                ...values,
                err: err
            })
        })
    };

    const handleClickShowPassword = () => {
        setValues({ ...values, showPassword: !values.showPassword });
    };

    const onChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value});
    };

    const onChecked = name => event => {
        setValues({...values,
            checked: {...values.checked, [name]: event.target.checked }});

        const options = routes;
        if (event.target.checked) {
            options.push(+event.target.value)
        } else {
           let index = options.indexOf(+event.target.value)
           options.splice(index, 1)
        }
        setRoutes(options);
        setData({...data, zones: routes});
    };

    const isChecked = name => {
        if(routes.indexOf(name)>=0)
            return true;

        if (values.checked === undefined)
            return false;

        const chk = (values.checked[name]);

        if(chk === undefined)
            return false;

        return chk;
    };

    return (
        <>
            <form onSubmit={e => onSubmit(e)}>
            <Grid container spacing={3}>
                <div className={'grid-title'}>
                    <p>Datos Bsicos</p>
                </div>
                {/*Nombre*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='text' label='Nombre'
                               name='name'
                               onChange={e => onChange(e)}
                               value={data.name.toUpperCase()}
                               required fullWidth autoFocus/>
                </Grid>
                {/*Apellido*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='text' label='Apellido'
                               name='surname'
                               onChange={e => onChange(e)}
                               value={data.surname.toUpperCase()}
                               required fullWidth />
                </Grid>
                {/*Direccin*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='address' label='Direccin'
                               name='address'
                               onChange={e => onChange(e)}
                               value={data.address}
                               required fullWidth />
                </Grid>
                {/*Telfono*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='phone' label='Telfono/Celular'
                               name='phones'
                               onChange={e => onChange(e)}
                               value={data.phones}
                               fullWidth />
                </Grid>

                {/*=================== DATOS DE SUELDO =====================*/}
                <div className={'grid-title'}>
                    <p>Datos de sueldo</p>
                </div>
                {/*Sueldo Base*/}
                <Grid item xs={12} sm={6}>
                    <TextField type='number' label='Sueldo Base'
                               name='sueldo'
                               onChange={e => onChange(e)}
                               value={data.sueldo}
                               required
                               fullWidth />
                </Grid>
                {/*Periodo de pago*/}
                <Grid item xs={12} sm={6}>
                    <InputLabel  id="pago_sueldo">Periodo de Pago</InputLabel>
                    <Select
                        labelId="pago_sueldo"
                        name={'pago_sueldo'}
                        value={data.pago_sueldo}
                        onChange={e => onChange(e)} fullWidth >
                        <MenuItem value={'SEMANAL'}>SEMANAL</MenuItem>
                    </Select>
                </Grid>

                {/*=================== CREDENCIALES =====================*/}
                <div className={'grid-title'}>
                    <p>Credenciales</p>
                </div>
                {/*Username*/}
                <Grid item xs={12} md={4}>
                    <TextField onChange={e => onChange(e)}
                               value = {data.username}
                               required fullWidth type="text" label="Nombre de usuario"
                               id="username"
                               name="username"
                               autoComplete="username"/>
                </Grid>

                {/*Password*/}
                <Grid item xs={12} md={4}>
                    <FormControl fullWidth>
                        <InputLabel htmlFor="standard-adornment-password">Password</InputLabel>
                        <Input
                            id="standard-adornment-password"
                            type={values.showPassword ? 'text' : 'password'}
                            value={data.password !== null ? data.password : ''}
                            name={'password'}
                            onChange={e => onChange(e)}
                            fullWidth
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        onClick={handleClickShowPassword}>
                                        {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            }
                        />
                    </FormControl>
                </Grid>


                {/*ADMINISTRADOR*/}
                <Grid item xs={12} md={4}>
                    <FormControl component="fieldset">

                    <FormControlLabel
                        control={
                            <Checkbox checked={data.advance} spacing={0}
                                      onChange={e => setData({
                                          ...data,
                                          admin: !data.admin,
                                      })}
                                      value="Admin" />
                        }
                        title={'Titulo'}
                        label="Administrador"
                    />
                        <FormHelperText style={{marginTop: 0}}>El administrador tiene todos los permisos</FormHelperText>
                    </FormControl>

                </Grid>

                {/*=================== ZONAS =====================*/}
                <div className={'grid-title'}>
                    <p>Asignacin de zonas</p>
                </div>
                <Grid item xs={12}>
                    <WithLoader loader={zones.loader} text={'Cargando Zonas'}>
                            {zones.data.map(z =>
                                <FormControlLabel key={z.id}
                                    control={
                                        <Checkbox name={'routes[]'} checked={isChecked(z.id)} onChange={onChecked(z.id)}  value={z.id} />
                                    }
                                    label={z.name}
                                />
                            )}
                    </WithLoader>
                </Grid>

                <Grid item xs={12} className='center'>
                    <Alert show={values.err!==null} message={values.err} />
                    { http.loader && <LinearProgress color="secondary" />}
                    <br/>
                    <Button type='submit' color='primary'
                            disabled={http.loader}
                            variant='contained'>
                        { method === 'post' ? 'Guardar' : 'Actualizar' }
                    </Button>
                </Grid>
            </Grid>
        </form>
        </>
    );
};


FrmEmploy.defaultProps = {
    method: 'post',
    url: '/employ',
    redirect: '/employ',
    initialData: {
        name: '', surname: '', address: '', phones: '', sueldo: '',
        pago_sueldo: '', username: '', admin: false, password: '', zones: []
    },
    initialRoutes: []
};

export default withRouter(FrmEmploy);
