import React from 'react';
import PropTypes from 'prop-types';
import {IconButton, Tooltip, withStyles} from "@material-ui/core";
import {Cancel, Info, MonetizationOn, MoneyOff, Payment} from "@material-ui/icons";
import BtnShowLoc from "../../_partials/_buttons/BtnShowLoc";
import BtnShowImg from "../../_partials/_buttons/BtnShowImg";
import {Link} from "react-router-dom";
import {CREDIT_SHOW, toName} from "../../../config/urls";

const BtnsPayOptions = ({pay, exec}) => {
    const HtmlTooltip = withStyles((theme) => ({
        tooltip: {
            backgroundColor: '#f5f5f9',
            color: 'rgba(0, 0, 0, 0.87)',
            maxWidth: 220,
            fontSize: theme.typography.pxToRem(12),
            border: '1px solid #dadde9',
        },
    }))(Tooltip);
    return (
        <>
            {/*Ver info de direccin y ubicacin*/}
            <HtmlTooltip
                title={
                    <>
                        <b>Direccin</b>
                        <p>{pay.address}{pay.ref_detail && <span>&nbsp;|&nbsp;{pay.ref_detail}</span>}</p>
                        {
                            pay.description &&
                            <>
                                <b>Descripcin</b>
                                <p>{pay.description}</p>
                            </>
                        }

                    </>
                }>
                <IconButton><Info /></IconButton>
            </HtmlTooltip>
            {/*Mostrar Geolocalizacin*/}
            <BtnShowLoc lon={pay.lon} lat={pay.lat} iconButton={true} />
            {/*Ver Imagen de referencia*/
                pay.ref_img && <BtnShowImg img={pay.ref_img}
                                           iconButton
                                           title={'Ver referencia'} />
            }
            {/*ver crdito*/}
            <Tooltip title={'Ver Crdito'}>
                <IconButton component={Link} to={toName(CREDIT_SHOW, {id: pay.credit_id})}>
                    <MonetizationOn />
                </IconButton>
            </Tooltip>
            {/*Marcar Como mora*/
                pay.status !== 2 &&
                <Tooltip title={'Marcar como Mora'}>
                    <IconButton onClick={ () => exec('mora', {id: pay.id})}>
                        <MoneyOff />
                    </IconButton>
                </Tooltip>
            }

            {/*Pagar*/
                pay.status !== 2 &&
                <Tooltip title={'Marcar como pagado'}>
                    <IconButton onClick={ () => exec('pay', {id: pay.id}) }>
                        <Payment />
                    </IconButton>
                </Tooltip>
            }

            {/*Anular*/
                pay.status === 2 &&
                <Tooltip title={'Anular Pago'}>
                    <IconButton onClick={ () => exec('cancel', {id: pay.id}) }>
                        <Cancel />
                    </IconButton>
                </Tooltip>
            }
        </>
    );
};

BtnsPayOptions.propTypes = {
    exec: PropTypes.func.isRequired,
    pay: PropTypes.object
};

export default BtnsPayOptions;
