import React from 'react';
import Master from "../Master";
import WithLoader from "../_partials/hoc/WithLoader";
import useGet from "../../utils/useGet";
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import FrmClient from "../client/_form/FrmClient";
import BtnShowPasswordChange from "./_buttons/BtnShowPasswordChange";

const EditAuth = () => {

    const user = useGet(`/user`);

    return (
        <>
            <Master>
                <WithLoader loader={user.loader}>
                    <Container maxWidth='md' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>person_edit</Icon> Mis Datos</h4>
                            <Divider />
                        </div>
                        {
                            user.data.person && <FrmClient method={'put'} url={`/client/${user.data.person.id}`}
                                                           initialData={user.data.person}>
                                <BtnShowPasswordChange />
                            </FrmClient>
                        }

                    </Container>
                </WithLoader>
            </Master>
        </>
    );
};

export default EditAuth;
