import React from 'react';
import {
    CssBaseline,
    Drawer,
    LinearProgress,
    Button,
    AppBar,
    makeStyles,
    Toolbar,
    Avatar,
    Icon,
    useTheme,
    Hidden, Menu, MenuItem
} from "@material-ui/core";

import IconButton from '@material-ui/core/IconButton';
import PropTypes from 'prop-types';
import {connect} from "react-redux";

import userService from '../config/user.service';
import {removeUser} from "../store/actionsCreators";
import DrawerCustom from "./_partials/DrawerCustom";
import {Link} from "react-router-dom";
import {USER_EDIT} from "../config/urls";

const Master = (props) => {
    const drawerWidth = 250;
    const barHeight = 55;
    const theme = useTheme();
    const { container } = props;

    const useStyles = makeStyles(theme => ({
        root: {
            display: 'flex',
        },
        nested: {
            paddingLeft: theme.spacing(3),
        },
        btnContent: {
            flexGrow: 1,
        },
        drawer: {
            [theme.breakpoints.up('md')]: {
                width: drawerWidth,
                flexShrink: 0,
            },
        },
        appBar: {
            height: barHeight,
            [theme.breakpoints.up('md')]: {
                width: `calc(100% - ${drawerWidth}px)`,
                marginLeft: drawerWidth,
            },
        },
        menuButton: {
            marginRight: theme.spacing(2),
            [theme.breakpoints.up('md')]: {
                display: 'none',
            },
        },
        toolbar: {
          height: barHeight
        },
        drawerPaper: {
            width: drawerWidth,
        },
        content: {
            flexGrow: 1,
            padding: theme.spacing(3),
            position: 'relative',
            top: barHeight
        },
    }));
    const classes = useStyles();

    const [mobileOpen, setMobileOpen] = React.useState(false);
    const [anchorEl, setAnchorEl] = React.useState(null);


    const handleMenuClick = event => { setAnchorEl(event.currentTarget);};
    const handleMenuClose = () => {setAnchorEl(null);};
    const handleDrawerToggle = () => {setMobileOpen(!mobileOpen);};

    const logout = () => {
        props.removeUser();
        userService.clearSession();
    };

    const renderMenuUser = (
        <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}>
            <MenuItem component={Link} to={USER_EDIT}>Mis datos</MenuItem>
            <MenuItem onClick={logout}>Cerrar Sesin</MenuItem>
        </Menu>
    );

    return (
        <>
            {props.user
                ?
                <div className={classes.root}>
                    <CssBaseline/>
                    <AppBar position="fixed" className={classes.appBar}>
                        <Toolbar>
                            <IconButton
                                color="inherit"
                                aria-label="Abrir men"
                                edge="start"
                                onClick={handleDrawerToggle}
                                className={classes.menuButton}>
                                <Icon>menu</Icon>
                            </IconButton>

                            <div className='right-menu'>
                                <Button
                                    aria-label="account of current user"
                                    aria-controls="menu-appbar"
                                    aria-haspopup="true"
                                    onClick={handleMenuClick}
                                    color="inherit"
                                >
                                    <Avatar className={'avatar-toolbar'}>{props.user.person.name.substring(0, 1)}</Avatar>&nbsp;
                                    { props.user.person.name }
                                </Button>
                            </div>
                        </Toolbar>
                    </AppBar>
                    { renderMenuUser }

                    <nav className={classes.drawer} aria-label="mailbox folders">
                        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                        <Hidden mdUp implementation="css">
                            <Drawer
                                container={container}
                                variant="temporary"
                                anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                                open={mobileOpen}
                                onClose={handleDrawerToggle}
                                classes={{
                                    paper: classes.drawerPaper,
                                }}
                                ModalProps={{
                                    keepMounted: true, // Better open performance on mobile.
                                }}>
                                <DrawerCustom classes={classes} />
                            </Drawer>
                        </Hidden>
                        <Hidden smDown implementation="css">
                            <Drawer classes={{paper: classes.drawerPaper,}}
                                variant="permanent" open>
                                <DrawerCustom classes={classes} />
                            </Drawer>
                        </Hidden>
                    </nav>
                    {/*Contenido*/}
                    <main className={classes.content}>{ props.children}</main>
                </div>
                :
                <div className="loader-page" ><LinearProgress color='secondary' /></div>
            }
        </>
    );
};

Master.propTypes = {
    container: PropTypes.instanceOf(typeof Element === 'undefined' ? Object : Element),
};

const mapDispatchToProps = dispatch => ({
    removeUser() {
        dispatch(removeUser());
    }
});

const mapStateToProps = (state) => ({
    user: state.user
});

export default connect(mapStateToProps, mapDispatchToProps)(Master);
