import React, {useState} from 'react';
import PropTypes from 'prop-types';
import DlgInput from "../../_partials/_modals/DlgInput";
import {toast} from "react-toastify";
import Axios from "axios";
import {processErrors} from "../../../utils/utils";


const DlgCancelPayment = ({open, onClose, selectPay, success}) => {
    const [loader, setLoader] = useState(false);

    const confirmCancel = (motivo) => {
        if (!selectPay) {
            toast.warn('Vuelva a seleccionar el pago que desea anular');
            return;
        }

        if (!motivo) {
            toast.warn('Es necesario especificar un motivo de anulacin');
            return;
        }
        setLoader(true);
        Axios.delete(`/payment/${selectPay}`, {data: {description: motivo}})
            .then(res => {
                if (res.ok) {
                    toast.success('El pago fue anulado con xito');
                    success(selectPay, 1);
                    onClose();
                }
            }).catch(err => {
            toast.error(processErrors(err));
        }).finally(() => setLoader(false));
    };

    return (
        <DlgInput open={open}
                  title={`Anular pago COD-${selectPay}`}
                  desc={`Por qu deseas anular este pago COD-${selectPay}?`}
                  inputLabel={'Motivo'}
                  loader={loader}
                  onConfirm={(v) => confirmCancel(v)}
                  onClose={onClose}/>
    );
};

DlgCancelPayment.propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    selectPay: PropTypes.number,
    success: PropTypes.func
};
DlgCancelPayment.defaultProps = {

};

export default DlgCancelPayment;
