import React, {useState} from 'react';
import {withRouter} from 'react-router-dom'
import {Grid, TextField, Button, Icon, LinearProgress} from "@material-ui/core";
import {KeyboardDatePicker} from "@material-ui/pickers";
import {toast} from "react-toastify";
import {Autocomplete} from '@material-ui/lab'
import {categories} from "../../../config/config";
import moment from "moment";
import Alert from "../../_partials/Alert";
import useHttp from "../../../utils/useHttp";
import {EXPENSE_LIST} from "../../../config/urls";
import useGet from "../../../utils/useGet";
import WithAdmin from "../../_partials/hoc/WithAdmin";
const FrmExpense = ({history}) => {

    const [data, setData] = useState({
        monto: '',
        category: '',
        description: '',
        image: null,
        date: null,
        selectedDate: null,
        showImage: null,
        employ_id: null
    });
    const [err, setErr] = useState(null);
    const http = useHttp('post', true);
    const employs = useGet('/employ');

    const onSubmit = (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('monto', data.monto);
        formData.append('category', data.category);
        formData.append('description', data.description);
        if (data.image)
            formData.append('image',data.image);
        if (data.date)
            formData.append('date', data.date);
        if (data.employ_id)
            formData.append('employ_id', data.employ_id);

        setErr(null);
        http.request(`/expense`, formData).then(res => {
            if(res.ok) {
                toast.success('Gasto procesado con xito');
                history.push(EXPENSE_LIST);
            }

        }).catch(err =>{
            setErr(err);
        })
    };

    const onChange = (e) => {

       const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value});
    };

    const onChangeDate = date => {
        setData({
            ...data,
            selectedDate: date,
            date: moment(date).format('YYYY-MM-DD')
        });
    };

    const onChangeFile = e => {
        if(e.target.files.length > 0) {
            setData({
                ...data,
                image: e.target.files[0],
                showImage: URL.createObjectURL(e.target.files[0])
            });
        }
    };

    const onChangeCategory = (option, value) => {
        if (value === null) {
            setData({
                ...data, category: null
            });
            return;
        }
        setData({
            ...data, category: value
        });
    };

    const onChangeEmploy = (option, value) => {
        if (value!==null){
            setData({
                ...data,
                employ_id: value.id,
            });
        }
        else {
            setData({...data, employ_id: null})
        }
    };

    return (
        <form onSubmit={(e) => onSubmit(e)}>
            <Grid container spacing={3}>
                {/*MONTO*/}
                <Grid item xs={12} sm={12}>
                    <TextField type='text' label='Monto $'
                               name='monto'
                               placeholder={'$0.00'}
                               onChange={e => onChange(e)}
                               value={data.monto}
                               required fullWidth autoFocus/>
                </Grid>
                {/*CATEGORA*/}
                <Grid item xs={12} sm={12}>
                    <Autocomplete
                        id="category"
                        name={'categories'}
                        freeSolo
                        value={data.category === null ? '' : data.category.toUpperCase()}
                        options={categories}
                        onChange={(o,v) => onChangeCategory(o, v)}
                        renderInput={params => (
                            <TextField {...params} label="Categora"
                                        onChange={e => onChange(e)}
                                       name={'category'} fullWidth required />
                        )}
                    />
                </Grid>
                {/*Date*/}
                <Grid item xs={12} sm={12}>
                    <KeyboardDatePicker
                        disableToolbar
                        variant="inline"
                        format="DD/MM/YYYY"
                        fullWidth
                        autoOk={true}
                        id="date"
                        label="Fecha del gasto"
                        value={data.selectedDate}
                        onChange={(date) => onChangeDate(date)}
                        KeyboardButtonProps={{
                            'aria-label': 'Cambiar Fecha',
                        }}
                    />
                </Grid>

                {/*Empleado*/}
                <WithAdmin>
                    <Grid item xs={12}>
                        <Autocomplete
                            id="employ_id"
                            options={employs.data}
                            onChange={(option, value) => onChangeEmploy(option, value)}
                            getOptionLabel={option => (option.name+' '+option.surname).toUpperCase() }
                            renderInput={params => (
                                <TextField
                                    {...params}
                                    label="Empleado" variant="standard" fullWidth />
                            )}
                        />
                    </Grid>
                </WithAdmin>

                {/*Descripcin*/}
                <Grid item xs={12}>
                    <TextField
                        id="description"
                        label="Detalle | Observacin"
                        multiline
                        rows="3"
                        fullWidth
                        name='description'
                        onChange={e => onChange(e)}
                        value={data.description}
                    />
                </Grid>

                {/*Comprobante*/}
                <Grid item xs={12} sm={12} style={{justifyContent: 'center', display: 'flex', flexDirection: 'column'}}>
                    {
                        data.showImage &&
                        <>
                            <div style={{padding: 10}}>
                                <img src={data.showImage} alt="Preview" width="100%"/>
                            </div>
                            <Button variant={'outlined'}
                                    onClick={() => setData({...data, showImage: null, image: null})}
                                    color={'secondary'}>Quitar Comprobante</Button>
                        </>
                    }

                    {!data.showImage &&
                    <Button
                        variant="outlined"
                        component="label"
                        color={'primary'}>
                        <Icon>photo</Icon>&nbsp;
                        Agregar Comprobante
                        <input
                            type="file"
                            accept="image/*"
                            style={{ display: "none" }}
                            onChange={(e) => onChangeFile(e)}
                        />
                    </Button> }
                </Grid>
                {/*Submit form*/}
                <Grid item xs={12} className='center'>
                    <Alert show={err!==null} message={err} />
                    { http.loader && <LinearProgress color="secondary" />}
                    <br/>
                    <Button type='submit' color='primary'
                            disabled={http.loader}
                            variant='contained'>
                        Guardar Gasto
                    </Button>
                </Grid>
            </Grid>
        </form>
    );
};

export default withRouter(FrmExpense);
