import React from 'react';
import {withRouter} from 'react-router-dom';
import {Button, Container, Paper} from "@material-ui/core";
import {LOGIN_REDIRECT} from "../../config/urls";
import {WifiOff, Refresh} from '@material-ui/icons';
const NotConnected = () => {

    return (
        <Container maxWidth='sm' component={Paper} className='paper-content form-login-content'>
            <div className={'forbidden-content '}>
                <h3>No se ha podido comunicar con el servidor</h3>
                <WifiOff className={'forbidden-icon'} />
                <p>Revisa tu conexin a internet, puede que el cable o el adaptador de WiFi este desconectado.</p>
                <div>
                    <Button color={'primary'}
                            startIcon={<Refresh />}
                            variant={'contained'}
                            onClick={() => window.location.href = LOGIN_REDIRECT}>Reintentar</Button>
                </div>
            </div>
        </Container>
    );
};

export default withRouter(NotConnected);
