import React, {useState} from 'react';
import {Button} from "@material-ui/core";
import DlgPassword from "../_dialog/DlgPassword";

const BtnShowPasswordChange = () => {
    const [open, setOpen] = useState(false);

    return (
        <>
        <Button onClick={() => setOpen(true)} className={'ml-1'}>
          Cambiar contrasea
        </Button>
        <DlgPassword onClose={() => setOpen(false)} open={open}/>
        </>
    );
};

export default BtnShowPasswordChange;
