import React, {useState} from 'react';
import {Button, IconButton, Tooltip} from "@material-ui/core";
import PropTypes from 'prop-types';
import {NotListedLocation} from "@material-ui/icons";
import DlgViewLoc from "../_modals/DlgViewLoc";

const BtnShowLoc = ({lat, lon, title, description, iconButton}) => {
    const [openDialog, setOpenDialog] = useState(false);

    const openDialogImg = () => {
        setOpenDialog(true);
    };

    return (
        <>
            {
                iconButton &&
                <Tooltip title={title}>
                    <IconButton onClick={() => openDialogImg()}
                                disabled={(!lat || !lon)}
                                aria-label="Ver ubicacin">
                        <NotListedLocation />
                    </IconButton>
                </Tooltip>
            }

            { !iconButton &&
            <Button
                disabled={(!lat || !lon)}
                onClick={() => openDialogImg()}
                variant="text"
                size={'small'}
                color="default"
                startIcon={<NotListedLocation />}
            >{title}</Button>
            }

            <DlgViewLoc lat={parseFloat(lat)} lon={parseFloat(lon)}
                        description={description}
                        isOpen={openDialog} onClose={() => setOpenDialog(false)}/>
        </>
    );
};

BtnShowLoc.propTypes = {
    lon: PropTypes.string.isRequired,
    lat: PropTypes.string.isRequired,
    title: PropTypes.string
};

BtnShowLoc.defaultProps = {
    title: 'Ver Ubicacin',
    description: ''
};

export default BtnShowLoc;
