import React from 'react';
import TableListCredit from "./_table/TableListCredit";
import Master from "../Master";
const HistoryClientCredit = ({match}) => {
    const id = match.params.id;
    return (
        <Master>
            <TableListCredit personId={id} searchable={false} />
        </Master>
    );
};

export default HistoryClientCredit;
