import React from 'react';
import FrmPayRoll from "./_form/FrmPayRoll";
import useGet from "../../utils/useGet";
import Master from "../Master";
import {Container, Divider, Icon, Paper, Grid} from "@material-ui/core";
import WithAdmin from "../_partials/hoc/WithAdmin";

const RegisterPayRoll = () => {
    const employs = useGet('/employ');

    return (
        <Master>
            <WithAdmin>
                <Container maxWidth='lg' component={Paper} className='paper-content'>
                    <div className='paper-header'>
                        <h4> <Icon>payment</Icon> Pagar sueldo</h4>
                        <Divider />
                    </div>
                    <Grid container>
                        <Grid item xs={12} md={12}>
                            <FrmPayRoll employs={employs}/>
                        </Grid>
                    </Grid>
                </Container>
            </WithAdmin>
        </Master>
    );
};

export default RegisterPayRoll;
