import React, {useState} from 'react';
import {withRouter} from 'react-router-dom';
import {Button, Grid, LinearProgress, TextField} from "@material-ui/core";
import useHttp from "../../../utils/useHttp";
import {toast} from "react-toastify";
import {CLIENT_LIST} from "../../../config/urls";
import Alert from "../../_partials/Alert";

const FrmClient = ({ method, url, initialData, history, children }) => {

    const http = useHttp(method);
    const [err, setErr] = useState(null);
    const [data, setData] = useState(initialData);

    const onSubmit = (e) => {
        e.preventDefault();
        setErr(null);
        http.request(url, data).then(res => {
            if(res.ok && method==='post') {
                toast.success('Cliente Guardado con xito');
                history.push(CLIENT_LIST);
            }

            if(res.ok && method==='put') {
                toast.success(res.message);
            }
        }).catch(err =>{
            setErr(err);
        })
    };


    const onChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value});
    };

    return (
        <div>
            <form onSubmit={e => onSubmit(e)}>
                <Grid container spacing={3}>
                    <div className={'grid-title'}>
                        <p>Datos Bsicos</p>
                    </div>
                    {/*Nombre*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='text' label='Nombre'
                                   name='name'
                                   onChange={e => onChange(e)}
                                   value={data.name.toUpperCase()}
                                   required fullWidth autoFocus/>
                    </Grid>
                    {/*Apellido*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='text' label='Apellido'
                                   name='surname'
                                   onChange={e => onChange(e)}
                                   value={data.surname.toUpperCase()}
                                   fullWidth />
                    </Grid>
                    {/*Direccin*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='address' label='Direccin'
                                   name='address'
                                   onChange={e => onChange(e)}
                                   value={data.address}
                                   required fullWidth />
                    </Grid>

                    {/*Email*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='email' label='Correo: '
                                   name='email'
                                   onChange={e => onChange(e)}
                                   value={data.email === null ? '' : data.email}
                                   fullWidth />
                    </Grid>

                    {/*Telfono Personal*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='phone' label='Telfono Personal'
                                   name='phones'
                                   inputProps={{maxLength: 13}}
                                   onChange={e => onChange(e)}
                                   value={data.phones}
                                   fullWidth />
                    </Grid>

                    {/*Telfono Trabajo*/}
                    <Grid item xs={12} sm={6}>
                        <TextField type='phone' label='Telfono Trabajo'
                                   name='phones_b'
                                   inputProps={{maxLength: 13}}
                                   onChange={e => onChange(e)}
                                   value={data.phones_b}
                                   fullWidth />
                    </Grid>



                    {/*Submit btn*/}
                    <Grid item xs={12} className='center'>
                        <Alert show={err!==null} message={err} />
                        { http.loader && <LinearProgress color="secondary" />}
                        <br/>
                        <Button type='submit' color='primary'
                                disabled={http.loader}
                                variant='contained'>
                            { method === 'post' ? 'Guardar' : 'Actualizar' }
                        </Button>
                        { children }
                    </Grid>
                </Grid>
            </form>
        </div>
    );
};

FrmClient.defaultProps = {
    method: 'post',
    url: 'client',
    initialData: {
        name: '',
        surname: '',
        address: '',
        phones: '',
        phones_b: '',
        email: ''
    }
};

export default withRouter(FrmClient);
