import React from 'react';
import PropTypes from "prop-types";
import {Button, Dialog, DialogActions, DialogContent, useMediaQuery, useTheme} from "@material-ui/core";
import MapViewLoc from "../../maps/MapViewLoc";

const DlgViewLoc = ({isOpen, title, lon, lat, onClose, description}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    return (
        <Dialog
            fullScreen={fullScreen}
            open={isOpen}
            maxWidth={'md'}
            onClose={() => onClose()}
            aria-labelledby="responsive-dialog-title">
            <DialogContent className={'map-dialog'}>
                <div className={'dialog-map-header'}>
                    <h2>{title}</h2>
                    <small className={'text-muted'}>{description}</small>
                </div>
                <MapViewLoc lon={lon} lat={lat} title={title} description={description} />
            </DialogContent>
            <DialogActions>
                <Button autoFocus onClick={() => onClose()} color="primary">
                    OK
                </Button>
            </DialogActions>
        </Dialog>
    );
};

DlgViewLoc.propTypes = {
    onClose: PropTypes.func,
    isOpen: PropTypes.bool,
    lon: PropTypes.number.isRequired,
    lat: PropTypes.number.isRequired,
    title: PropTypes.string
};

DlgViewLoc.defaultProps = {
    isOpen: false,
    title: 'Ver Ubicacin',
    description: ''
};

export default DlgViewLoc;
