import React from 'react';
import {Grid} from '@material-ui/core';
import useGet from "../../utils/useGet";
import Master from "../Master";
import BoxInfoCredit from "./_partials/BoxInfoCredit";
import StatusCredit from "./_partials/StatusCredit";
import {money} from "../../config/config";
import PayCobroCredit from "./_partials/PayCobroCredit";
import ClientDataCredit from "./_partials/ClientDataCredit";
import DataCredit from "./_partials/DataCredit";
import ViewPayments from "../payments/_partials/ViewParyments";
import SKShowCredit from "./_skeleton/SkShowCredit";

const ShowCredit = ({match}) => {
    const id = match.params.id;
    const credit = useGet(`/credit/${id}`);

    return (
        <Master>
            <SKShowCredit loader={credit.loader}>
            <Grid container spacing={1}>
                <Grid item xs={12} md={12}>
                    <Grid container>
                        <BoxInfoCredit md={3} color={'primary'} icon={'credit_card'}
                                       info={`${money(credit.data.monto)}`} text={'MONTO INICIAL'} />

                        <BoxInfoCredit color={'deal'} icon={'money'}
                                       info={`% ${credit.data.utilidad}`} text={'UTILIDAD'} />

                        <BoxInfoCredit md={3} color={'cyan'} icon={'local_atm'}
                                       info={`${money(credit.data.total_utilidad)}`} text={'SUBT. UTILIDAD'} />

                        <BoxInfoCredit color={'purple'} icon={'attach_money'}
                                       info={`${money(credit.data.total)}`} text={'TOTAL'} />

                        <BoxInfoCredit  color={'red'} icon={'exposure_plus_1'}
                                  info={`${credit.data.n_pagos}`} text={'# DE PAGOS'} />
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={1}>
                        <Grid item xs={12} md={6}>
                            <StatusCredit description={credit.data.description} status={credit.data.status} />
                            {/*Datos del Pagos*/}
                            <PayCobroCredit description={credit.data.description} plazo={credit.data.plazo} cobro={credit.data.cobro} />
                            {/*Datos del cliente*/}
                            <ClientDataCredit name={credit.data.client_name}
                                              surname={credit.data.client_surname}
                                              address={credit.data.client_address}
                                              clientId={credit.data.person_id}/>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            {/*Datos del crdito*/}
                            <DataCredit credit={credit.data} />
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item xs={12}>
                    {
                        credit.data.id && credit.data.status !== 0 && <ViewPayments creditId={credit.data.id} />
                    }
                </Grid>

            </Grid>
            </SKShowCredit>
        </Master>
    );
};




export default ShowCredit;

