import React, {useRef, useState} from 'react';
import {Container, Paper, Grid, Button, Icon, IconButton} from "@material-ui/core";
import {KeyboardDatePicker} from "@material-ui/pickers";
import moment from "moment";
import PropTypes from 'prop-types';
import SelectEmploy from "../../employ/_partials/SelectEmploy";
const ExpenseFilter = ({onFilter, filters, onClearFilters}) => {

    const [data, setData] = useState(filters);
    const selectERef = useRef();

    const onChangeDate = (name, date) => {
        setData({
            ...data,
            [name]: date,
        });
    };

    const onChangeEmploy = (employ) => {
        if (employ) {
            setData({...data, employ_id: employ.id });
        } else {
            setData({...data, employ_id: null });
        }
    };

    const filterSend = () => {
      onFilter({
          dateFrom: data.dateFrom, dateTo: data.dateTo, employ_id:  data.employ_id });
    };

    const clearFilters = () => {
        setData({...data, dateFrom: null, dateTo: null, employ_id: null});
        onClearFilters();
        selectERef.current.reset();
    };

    return (
        <>
            <Container component={Paper} elevation={0} style={{marginBottom: '1rem'}}>
                <Grid container spacing={3} alignItems={'center'} >
                    <Grid item xs={12} sm={6} md={3}>
                        <KeyboardDatePicker
                        disableToolbar
                        variant="inline"
                        format="DD/MM/YYYY"
                        fullWidth
                        autoOk={true}
                        id="date"
                        label="Fecha Inicial"
                        value={data.dateFrom}
                        onChange={(date) => onChangeDate('dateFrom',date)}
                        KeyboardButtonProps={{
                            'aria-label': 'Ingresar fecha inicial',
                        }}
                    />
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <KeyboardDatePicker
                            disableToolbar
                            variant="inline"
                            format="DD/MM/YYYY"
                            fullWidth
                            autoOk={true}
                            id="date_end"
                            label="Fecha Final"
                            value={data.dateTo}
                            onChange={(date) => onChangeDate('dateTo', date)}
                            KeyboardButtonProps={{
                                'aria-label': 'Ingresar fecha final',
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} md={3} >
                        <SelectEmploy ref={selectERef}
                            onChange={(e) => onChangeEmploy(e)} />
                    </Grid>

                    <Grid item xs={12} sm={12} md={3} className={'d-flex'}>
                        <Button variant={'text'} color={'primary'} fullWidth onClick={() => filterSend()}>
                            <Icon>filter_list</Icon> &nbsp; Filtrar
                        </Button>
                        <IconButton onClick={() => clearFilters()}>
                            <Icon>clear_two_tone</Icon>
                        </IconButton>
                    </Grid>
                </Grid>
            </Container>

        </>
    );
};

ExpenseFilter.defaultProps = {
    onFilter: null,
    filters: {
        dateFrom: moment().clone().weekday(0),
        dateTo: moment().clone().weekday(5),
        employ_id: null,
    }
};

ExpenseFilter.propTypes = {
    onFilter: PropTypes.func
};

export default ExpenseFilter;
