import React from 'react';
import PropTypes from 'prop-types';
import {Chip, Grid, TextField} from "@material-ui/core";
import WithLoader from "../../_partials/hoc/WithLoader";
import useGet from "../../../utils/useGet";

const EmployByUser = ({userId}) => {

    const employ = useGet(`/employ/user/${userId}`);

    return (
        <WithLoader loader={employ.loader} elevation={0}>
            <Grid container spacing={2}>
                <Grid item sm={6}>
                    <TextField readOnly label={'Nombre'} inputProps={{ className: 'text-upper'}}
                               value={`${employ.data.name} `}
                               fullWidth />
                </Grid>
                <Grid item sm={6}>
                    <TextField readOnly label={'Usuario'}
                               value={employ.data.username}
                               fullWidth />
                </Grid>

                <Grid item sm={6}>
                    <TextField readOnly label={'Email'}
                               value={employ.data.email}
                               fullWidth />
                </Grid>

                <Grid item sm={6}>
                    <TextField readOnly label={'Telfono'}
                               value={employ.data.phones}
                               fullWidth />
                </Grid>

                <Grid item sm={6}>
                    <TextField readOnly label={'Direccin'}
                               value={employ.data.address}
                               multiline={true}
                               fullWidth />
                </Grid>

                <Grid item sm={6} className={'d-flex center'}>
                    <b className={'mr-1'}>Estado:</b>
                    {
                        employ.data.status === 1 &&  <Chip label={"Activo"} className={'bg-green'} />
                    }
                    {
                        employ.data.status === 0 &&  <Chip label={"Inactivo"} className={'bg-red'} />
                    }
                    {
                        employ.data.status === -1 &&  <Chip label={"Eliminado"} className={'bg-red'} />
                    }
                </Grid>
            </Grid>
        </WithLoader>
    );
};

EmployByUser.propTypes = {
    userId: PropTypes.number.isRequired
};

export default EmployByUser;
