import React, {useState} from 'react';
import PropTypes from 'prop-types';
import {Button, Icon, IconButton, Tooltip} from "@material-ui/core";
import DlgViewEmployUser from "../_dialog/DlgViewEmployUser";

const BtnPreviewEmploy = ({userId, title, iconButton}) => {
    const [openDialog, setOpenDialog] = useState(false);

    const show = () => {
        setOpenDialog(true);
    };

    return (
        <>
            {
                iconButton &&
                <Tooltip title={title}>
                    <IconButton onClick={() => show()}
                                disabled={(!userId)}
                                aria-label={title}>
                        <Icon>person</Icon>
                    </IconButton>
                </Tooltip>
            }

            { !iconButton &&
            <Button
                disabled={(!userId)}
                onClick={() => show()}
                variant="text"
                size={'small'}
                color="default"
                startIcon={<Icon>person</Icon>}
            >{title}</Button>
            }

            <DlgViewEmployUser userId={userId}
                               onClose={() => setOpenDialog(false)}
                               open={openDialog}/>
        </>
    );
};

BtnPreviewEmploy.propTypes = {
    userId: PropTypes.number.isRequired,
    iconButton: PropTypes.bool,
    title: PropTypes.string
};

BtnPreviewEmploy.defaultProps = {
    iconButton: false,
    title: 'Ver empleado'
};

export default BtnPreviewEmploy;
