import React from 'react';
import FrmZone from "./_form/FrmZone";
import Master from "../Master";
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import WithAdminContent from "../_partials/hoc/WithAdminContent";

const RegisterZone = () => {
    return (
        <Master>
            <WithAdminContent>
                <Container maxWidth='sm' component={Paper} className='paper-content'>
                    <div className='paper-header'>
                        <h4> <Icon>map</Icon> Agregar Rutas/Zonas</h4>
                        <Divider />
                    </div>
                    <FrmZone method={'post'} initialZone={{name: '', description: ''}} />
                </Container>
            </WithAdminContent>
        </Master>
    );
};

export default RegisterZone;
