import React from 'react';
import Master from "../Master";
import MUIDataTable from 'mui-datatables'
import {
    Chip,
    Icon,
    IconButton,
} from "@material-ui/core";

import useGet from "../../utils/useGet";
import useHttp from "../../utils/useHttp";
import WithLoader from "../_partials/hoc/WithLoader";
import WithAdminContent from "../_partials/hoc/WithAdminContent";
import {tableOptions} from "../../config/config";
import {toast} from "react-toastify";
import {Link} from "react-router-dom";
import {PAYROLL_SHOW, toName} from "../../config/urls";

const ListPayRoll = () => {

    const pays = useGet('/payroll');

    const columns = [
        { label: 'NOMBRE', name: 'name',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}
            }
        },
        { label: 'APELLIDO', name: 'surname',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}
            }
        },
        { label: 'CONCEPTO', name: 'concept'},
        { label: 'FECHA', name: 'date', options: {filter: false}},
        { label: 'OBSERV.', name: 'description',
            options: {
            filter: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (<div>{value ? value: 'Ninguna'}</div>)
            }
        },
        },
        { label: 'TOTAL', name: 'total', options: {filter: false}},
        { label: 'ESTADO', name: 'status',
            options: {
                filter: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            { value === 1 &&
                            <>
                                <Chip
                                    label={"Correcto"}
                                    className={'bg-green'}/>
                            </>
                            }
                            { value === -1 &&
                            <>
                                <Chip
                                    label="Anulado"
                                    className={'bg-red'}/>
                            </>
                            }
                        </div>
                    );
                }
            }
        },
        {
            label: 'OPCIONES', name: 'id',
            options: {
                filter: false,
                sort: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            <IconButton onClick={() => onSubmit(value)}
                                        size={"small"}
                                        aria-label="Anular">
                                <Icon>block</Icon>
                            </IconButton>

                            <IconButton component={Link} to={toName(PAYROLL_SHOW, {id: value})}
                                        size={"small"}
                                        aria-label="Ver">
                                <Icon>visibility</Icon>
                            </IconButton>
                        </div>
                    );
                },
            }
        }
    ];
    const invalidatePay = useHttp('delete');

    const onSubmit = (selectedId) => {
        let description = window.prompt('Ingrese el motivo de la anulacin para confirmar');

        if (!description) return;

        invalidatePay.request(`/payroll/${selectedId}`, {description: description})
            .then(res => {
                if (res.ok) {
                    let index = pays.data.findIndex(p => p.id === selectedId);
                    pays.data[index].status = -1;
                    toast.success(res.message);
                    const data = pays.data;
                    pays.setData();
                    pays.setData(data);
                }
            }).catch(err => {
                toast.error(err);
            });
    };

    return (
        <Master>
            <WithLoader loader={pays.loader} text={'Cargando roles...'}>
                <WithAdminContent>
                    <MUIDataTable
                        columns={columns}
                        data={pays.data}
                        options={tableOptions}
                        title={'Roles de pagos'}/>
                </WithAdminContent>
            </WithLoader>
        </Master>
    );
};

export default ListPayRoll;
