import React from 'react';
import PropTypes from 'prop-types';
import {Button, Dialog, DialogActions, DialogContent, useMediaQuery, useTheme} from "@material-ui/core";
import EmployByUser from "../_partials/EmployByUser";

const DlgViewEmployUser = ({userId, title, open, onClose}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    return (
        <Dialog
            fullScreen={fullScreen}
            open={open}
            maxWidth={'md'}
            onClose={() => onClose()}
            aria-labelledby="responsive-dialog-title">
            <DialogContent className={'map-dialog'}>
                <div className={'dialog-map-header'}>
                    <h2>{title}</h2>
                </div>
                {
                    userId &&  <EmployByUser userId={userId} />
                }
            </DialogContent>
            <DialogActions>
                <Button autoFocus onClick={() => onClose()} color="primary">
                    OK
                </Button>
            </DialogActions>
        </Dialog>
    );
};

DlgViewEmployUser.propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    userId: PropTypes.number,
    title: PropTypes.string
};

DlgViewEmployUser.defaultProps = {
    title: 'Ver Empleado'
};

export default DlgViewEmployUser;
