import React from 'react';
import FrmEmploy from "./_form/FrmEmploy";
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import Master from "../Master";

const RegisterEmploy = () => {
    return (
        <>
            <Master>
                <Container maxWidth='md' component={Paper} className='paper-content'>
                    <div className='paper-header'>
                        <h4> <Icon>person_add</Icon> Agregar Empleado</h4>
                        <Divider />
                    </div>
                    <FrmEmploy method={'post'} />
                </Container>
            </Master>

        </>
    );
};

export default RegisterEmploy;
