import React from 'react';
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import FrmEmploy from "./_form/FrmEmploy";
import Master from "../Master";
import useGet from "../../utils/useGet";
import WithLoader from "../_partials/hoc/WithLoader";

const EditEmploy = ({match}) => {
    const id = match.params.id;

    const employ = useGet(`/employ/${id}`);


    return (
        <>
            <Master>
                <WithLoader loader={employ.loader} text={'Cargando datos del empleado...'}>
                    <Container maxWidth='md' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>person_edit</Icon> Editar Empleado</h4>
                            <Divider />
                        </div>
                        {
                            employ.data.name !== undefined &&
                                <FrmEmploy method={'put'} url={`/employ/${id}`}
                                           initialData={employ.data}
                                           initialRoutes={employ.data.zones}/>
                        }
                    </Container>
                </WithLoader>
            </Master>
        </>
    );
};

export default EditEmploy;
