import React from 'react';
import {Grid, Paper, TextField} from "@material-ui/core";
import {toDate, toDateHour} from "../../../utils/utils";
import RefPrendaCredit from "./_tabs/RefPrendaCredit";
import BtnShowLoc from "../../_partials/_buttons/BtnShowLoc";
import BtnPreviewEmploy from "../../employ/_buttons/BtnPreviewEmploy";

const DataCredit = ({credit}) => {
    return (
        <>
            <Paper style={{borderRadius: 0}} className={'p-1'}>
                <div className={'title-muted'}>Datos del crdito</div>
                <Grid container style={{marginTop: '1rem'}} spacing={1}>
                    <Grid item xs={6}>
                        <TextField readOnly label={'Fecha inicio'} fullWidth
                                   value={ credit.f_inicio ? toDate(credit.f_inicio) : 'dd/mm/yyyy'}/>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField readOnly label={'Fecha fin'}
                                    fullWidth
                                   value={ credit.f_fin ? toDate(credit.f_fin): 'dd/mm/yyyy'} />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField readOnly label={'Fecha de Registro'} value={ credit.created_at ? toDateHour(credit.created_at): 'dd/mm/yyyy'} fullWidth/>
                    </Grid>
                    <Grid item xs={6} className={'d-flex center'} >
                        <TextField readOnly value={credit.ruta ? credit.ruta: 'Desconocida'} label={'Ruta/Zona'} fullWidth/>
                    </Grid>
                    <Grid item xs={12}>

                        {
                            credit.geo_lat && credit.geo_lon && <BtnShowLoc lat={credit.geo_lat}
                                                                            lon={credit.geo_lon} description={credit.address} />
                        }
                        {
                            credit.user_id && <BtnPreviewEmploy userId={credit.user_id} title={'Empleado'} />
                        }

                    </Grid>
                </Grid>
            </Paper>
            <Paper>
                {/*Prendas y Referencias*/}
                <RefPrendaCredit ref_detail={credit.ref_detail}
                                 ref_img={credit.ref_img} prenda={credit.prenda}/>
            </Paper>

        </>
    );
};

export default DataCredit;
