import React, {useState} from 'react';
import {TextField, Grid, Button, LinearProgress} from "@material-ui/core";
import {toast} from "react-toastify";
import { withRouter } from 'react-router-dom'

import useHttp from "../../../utils/useHttp";
import Alert from "../../_partials/Alert";
import {ZONE_LIST} from "../../../config/urls";

const FrmZone = ({method, url, history, redirect, initialZone}) => {

    const [data, setData] = useState(initialZone);
    const [err, setErr] = useState(null);
    const http = useHttp(method);

    const onChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value})
    };

    const onSubmit = (e) => {
      e.preventDefault();
      setErr(null);
      http.request(url, data).then(res => {
        if(res.ok) {
            toast.success(`Zona ${ method === 'post' ? 'guardada' : 'actualizada' } con xito`);
            if(method ==='post'){
                setData({ name: '', description: ''});
                history.push(redirect);
            }

        }
      }).catch(e => {
          setErr(e);
      });
    };

    return (
        <form onSubmit={e => onSubmit(e)}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={12}>
                            <TextField type='text' label='Nombre de ruta'
                                       name='name'
                                       onChange={e => onChange(e)}
                                       value={data.name}
                                       required fullWidth />
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <TextField type='text' label='Descripcin'
                                       name='description'
                                       onChange={e => onChange(e)}
                                       value={data.description === null ? '' : data.description}
                                       fullWidth/>
                        </Grid>
                        <br/>
                        <Grid item xs={12} className='center'>
                            <Alert show={err!==null} message={err} />
                            { http.loader && <LinearProgress color="secondary" />}
                            <br/>
                            <Button type='submit' color='primary'
                                    disabled={http.loader}
                                    variant='contained'>
                                { method === 'post' ? 'Guardar' : 'Actualizar' }
                            </Button>
                        </Grid>
                    </Grid>
    </form>
    );
};
FrmZone.defaultProps = {
    method: 'post',
    url: '/route',
    redirect: ZONE_LIST,
    initialZone: {
        name: '',
        description: ''
    }
};

export default withRouter(FrmZone);
