import React from 'react';
import {makeStyles, withStyles, Table, TableRow, TableCell, TableContainer, TableBody} from "@material-ui/core";
import {AppBar, Tabs, Tab} from '@material-ui/core';
import TabPanel from "../../../_partials/TabPanel";
import BtnShowImg from "../../../_partials/_buttons/BtnShowImg";

const RefPrendaCredit = ({ref_detail, ref_img, prenda}) => {
    const classes = useStyles();
    const [value, setValue] = React.useState('one');
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div>
            <AppBar position="static" className={classes.root} elevation={0}>
                <StyledTabs value={value} onChange={handleChange}>
                    <StyledTab value="one" label={`Referencias (${ref_detail ? 1: 0})`} {...a11yProps('one')} />
                    <StyledTab value="two" label={ `Prendas (${prenda ? prenda.length : 0})`} {...a11yProps('two')} />
                </StyledTabs>

                {/*REFERENCIAS*/}
                <TabPanel value={value} index={'one'}>
                    <TableContainer>
                        <Table>
                            <TableBody>
                                {
                                    (ref_detail || ref_img ) &&
                                    <TableRow>
                                        <TableCell width={'30%'}>
                                            { ref_img ? <BtnShowImg img={ref_img} title={'Ver Ref.'} />   : ''}
                                        </TableCell>
                                        <TableCell>
                                            { ref_detail ? ref_detail : 'No hay referencia'}
                                        </TableCell>
                                    </TableRow>
                                }
                                {
                                    (!ref_detail && !ref_img) &&
                                    <TableRow>
                                        <TableCell colSpan={2}>
                                            <div className={'text-muted text-center pt-1'}>No existen referencias</div>
                                        </TableCell>
                                    </TableRow>
                                }
                            </TableBody>
                        </Table>
                    </TableContainer>
                </TabPanel>

                {/*PRENDAS*/}
                <TabPanel value={value} index={'two'}>
                    <TableContainer>
                        <Table>
                            <TableBody>
                            {
                                prenda && prenda.map(p => (
                                    <TableRow key={p.id}>
                                        <TableCell width={'30%'}>
                                            <BtnShowImg img={p.img} title={'Ver Prenda'}/>
                                        </TableCell>
                                        <TableCell>{p.detail}</TableCell>
                                    </TableRow>
                                ))
                            }
                            {
                                prenda.length === 0 &&
                                <TableRow>
                                    <TableCell colSpan={2} >
                                        <div className={'text-muted text-center pt-1'}>No existen prendas</div>
                                    </TableCell>
                                </TableRow>
                            }
                            </TableBody>
                        </Table>
                    </TableContainer>
                </TabPanel>
            </AppBar>
        </div>
    );
};

const a11yProps = (index) => {
    return {
        id: `wrapped-tab-${index}`,
        'aria-controls': `wrapped-tabpanel-${index}`,
    };
};

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        color: 'black'
    },
}));
const StyledTab = withStyles(theme => ({
    root: {
        textTransform: 'none',
        color: 'black',
        fontWeight: theme.typography.fontWeightRegular,
        fontSize: theme.typography.pxToRem(15),
        marginRight: theme.spacing(1),
    },
}))(props => <Tab disableRipple {...props} />);
const StyledTabs = withStyles({
    root: {
        backgroundColor: '#fafafa'
    }
})(props => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);


RefPrendaCredit.defaultProps = {
    prenda: []
};

export default RefPrendaCredit;
