import React from 'react';
import {GoogleApiWrapper} from 'google-maps-react';
import {CircularProgress, Paper} from "@material-ui/core";

const Loading = () => <Paper elevation={0} className="loader-container"> <CircularProgress color="secondary"/> <p>Cargando mapa...</p> </Paper>;

export default (WrappedComponent) => {
    const WithMapKey = ({...props}) => <WrappedComponent {...props} />

    return GoogleApiWrapper({
        apiKey: 'AIzaSyAdoB5hIORuaqfcPStZX8r61NCu2vV7xwE',
        //libraries: ['places', 'visualization'],
        LoadingContainer: Loading
    })(WithMapKey)
}
