import React from 'react';
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import Master from "../Master";
import FrmExpense from "./_form/FrmExpense";

const RegisterExpense = () => {
    return (
        <Master>
            <Container maxWidth='sm' component={Paper} className='paper-content'>
                <div className='paper-header'>
                    <h4> <Icon>local_atm</Icon> Registrar Gasto</h4>
                    <Divider />
                </div>
                <FrmExpense />
            </Container>
        </Master>
    );
};

export default RegisterExpense;
