import React, {useState} from 'react';
import {Chip, Icon, IconButton, Tooltip} from "@material-ui/core";
import {ArrowDownward, ArrowUpward} from "@material-ui/icons";
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import MUIDataTable from "mui-datatables";
import {PersonAdd} from '@material-ui/icons'
import {CLIENT_ADD, CLIENT_EDIT, CREDIT_BY_CLIENT, toName} from "../../config/urls";
import useGet from "../../utils/useGet";
import useHttp from "../../utils/useHttp";
import Master from "../Master";
import WithLoader from "../_partials/hoc/WithLoader";
import {tableOptions} from "../../config/config";
import CustomSearchRender from "../_partials/CustomSearchRender";
import WithAdmin from "../_partials/hoc/WithAdmin";
import MoneyOffIcon from '@material-ui/icons/MoneyOff';
import DlgConfirm from "../_partials/_modals/DlgConfirm";

const ListClient = () => {
    const clients = useGet('/client');
    const search = useHttp('get');
    const remove = useHttp('delete');
    const status = useHttp('put');

    const [selectClient, setSelectClient] = useState(null);
    const [dlgConfirm, setDlgConfirm] = useState(false);

    const [q, setQ] = useState('');

    const columns = [
        {name: "id", label: 'ID', options: {display: 'false', filter: false}},
        {
            name: 'name', label: 'NOMBRE',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}}
        },
        {
            name: 'surname', label: 'APELLIDO',
            options: {
                filter: false,
                customBodyRender: (value) => {return value.toUpperCase();}}
        },
        {
            name: 'phones', label: 'TELFONO', options: {filter: false}
        },
        {
            name: 'rank', label: 'RANK', options: {filter: false}
        },
        {
          name: 'mora', label: 'MORATORIA', options: {
              customBodyRender: (value) => {
                  return <>
                      { value === 1 && <Chip label={'SI'} size={'small'} className={'bg-red'} />}
                      { value === 0 && <Chip label={'NO'} size={'small'} />}
                  </>
              }
            }
        },
        {
            name: 'status', label: 'ESTADO', options: {
                filter: true,
                customFilterListOptions: { render: v => `Estado: ${v}` },

                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            { value === 1 &&
                            <>
                                <Chip
                                    size={'small'}
                                    label={"Activo"}
                                    className={'bg-green'}/>
                                    <WithAdmin>
                                        <Tooltip title={'Dar de BAJA'}>
                                            <IconButton size={"small"}
                                                        onClick={() => changeStatus(tableMeta.rowData[0], updateValue)}
                                                        aria-label="Dar de baja">
                                                <ArrowDownward/>
                                            </IconButton>
                                        </Tooltip>
                                    </WithAdmin>
                            </>
                            }
                            { value === 0 &&
                            <>
                                <Chip
                                    size={'small'}
                                    label="Inactivo"
                                    className={'bg-red'}/>
                                    <Tooltip title={'Dar de ALTA'}>
                                        <IconButton size={"small"}
                                                    onClick={() => changeStatus(tableMeta.rowData[0], updateValue)}
                                                    aria-label="Dar de alta">
                                            <ArrowUpward/>
                                        </IconButton>
                                    </Tooltip>
                            </>
                            }
                        </div>
                    );
                }
            }
        },
        {
            label: 'OPCIONES', name: 'id',
            options: {
                filter: false,
                sort: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <WithAdmin>
                            <Tooltip title={'Poner este cliente en Mora'}>
                                <IconButton
                                    onClick={() => addSelectClient(value)}
                                    size={"small"}
                                    aria-label="Poner en cliente en Mora">
                                    <MoneyOffIcon/>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={'Historial de Crditos'}>
                                <IconButton component={Link}
                                            to={toName(CREDIT_BY_CLIENT, {id: value})}
                                            size={"small"}
                                            aria-label="Historial de crditos">
                                    <Icon>list_alt</Icon>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={'Editar Cliente'}>
                                <IconButton component={Link}
                                            to={toName(CLIENT_EDIT, {id: value})}
                                            size={"small"}
                                            aria-label="Editar">
                                    <Icon>edit</Icon>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={'Eliminar Cliente'}>
                                <IconButton onClick={() => deleteClient(value, updateValue)}
                                            size={"small"}
                                            aria-label="delete">
                                    <Icon>delete</Icon>
                                </IconButton>
                            </Tooltip>
                        </WithAdmin>
                    );
                },
            }
        }
    ];

    const changeStatus = (clientId, updateValue) => {
        status.request(`/client/cancel/${clientId}`).then(res => {
            if(res.ok) {
                let index = clients.data.findIndex(d => d.id === clientId);

                const status = clients.data[index].status;
                if (status === 1)
                    clients.data[index].status = 0;
                else
                    clients.data[index].status = 1;

                updateValue(clients.data[index].status);

                toast.success(res.message);
            }
        }).catch(err => {
            toast.error(err);
        });
    };

    const deleteClient = (clientId) => {
        if(!window.confirm('Esta seguro que desea eliminar este cliente?')) return;

        remove.request(`/client/${clientId}`).then(res => {
            if(res.ok){
                let index = clients.data.findIndex(d => d.id === clientId);
                clients.data.splice(index, 1);

                toast.success(res.message);
                const data = clients.data;
                clients.setData();
                clients.setData(data);
            }
        }).catch(err => toast.error(err));
    };

    const onSearch = (searchText) => {
        let url = '/client';
        if(searchText !== null)
            url = `/client/search?q=${searchText}`;
        setQ(searchText);
        search.request(url).then(res => {
            if (res.ok) {
                clients.setData(res.data);
            }
        });
    };

    const options = {
        ...tableOptions,
        searchText: q,
        serverSide: true,
        onTableChange: (action, tableState) => {
            switch (action) {
                case 'search':
                   onSearch(tableState.searchText);
                   break;
                default:
                    return;
            }
        },
        customSearchRender: (searchText, handleSearch, hideSearch, options) => {
            return (
                <CustomSearchRender
                    searchText={searchText}
                    onSearch={handleSearch}
                    onHide={hideSearch}
                    options={options}
                    loader={search.loader}
                />
            );
        }

    };

    const HeaderTitle = () => {
        return (
            <div className={'header-btn'}>
                <h3>CLIENTES</h3>
                <IconButton component={Link} to={CLIENT_ADD}>
                    <PersonAdd />
                </IconButton>
            </div>
        );
    };

    const addSelectClient = (clientId) => {
        setSelectClient(clientId);
        setDlgConfirm(true);
    };

    const markMora = () => {
        if (!selectClient) {
            toast.error('Vuelva a seleccionar un cliente primero');
            return;
        }

        status.request(`/client/mora/${selectClient}`).then(res => {
            if (res.ok) {
                setDlgConfirm(false);
                toast.success(res.message);
            }
        }).catch(err => toast.error(err));
    };

    return (
        <Master>
            <WithLoader loader={clients.loader}>
                    <MUIDataTable
                        title={<HeaderTitle/>}
                        data={clients.data}
                        columns={columns} options={options} />
            </WithLoader>
            <DlgConfirm onConfirm={ () => markMora()}
                        open={dlgConfirm}
                        onClose={() => setDlgConfirm(false)}
                        desc={'Estas seguro que deseas marcar este cliente en mora ??'}
                        title={`Marcar cliente CLI-${selectClient} en Mora`} />
        </Master>
    );
};

export default ListClient;
