import React from 'react';
import {Container, Divider, Grid, Icon, Paper, TextField} from "@material-ui/core";
import useGet from "../../utils/useGet";
import Master from "../Master";
import WithLoader from "../_partials/hoc/WithLoader";
import Alert from "../_partials/Alert";
import {asset} from './../../config/config'
const ShowExpense = ({match}) => {
    const id = match.params.id;
    const expense = useGet(`/expense/${id}`);
    return (
        <Master>
            <WithLoader loader={expense.loader}>
                <Container maxWidth='sm' component={Paper} className='paper-content'>
                    <div className='paper-header'>
                        <h4> <Icon>local_atm</Icon> Ver Gasto</h4>
                        <Divider />
                    </div>
                    <Grid container spacing={3}>
                        {/*MONTO*/}
                        <Grid item xs={12} sm={12}>
                            <TextField type='text' label='Monto $'
                                       readOnly
                                       value={expense.data.monto}
                                       fullWidth/>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <TextField type='text' label='Categora'
                                       readOnly
                                       value={expense.data.category}
                                       fullWidth/>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <TextField type='date' label='Fecha'
                                       readOnly
                                       value={expense.data.date}
                                       fullWidth/>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <TextField type='text' label='Empleado'
                                       readOnly
                                       value={expense.data.name ? expense.data.name.toUpperCase() :  ''}
                                       fullWidth/>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                            <TextField
                                label="Detalle | Observacin"
                                multiline
                                rows="3"
                                fullWidth
                                value={expense.data.description === null ? 'Ninguna' : expense.data.description }
                            />
                        </Grid>

                        <Grid item xs={12} sm={12}>
                            {
                                expense.data.image &&
                                <div style={{padding: 10}}>
                                    <img src={asset(expense.data.image)} alt="Preview" width="100%"/>
                                </div>
                            }
                             <Alert message={'No tiene comprobante'} type={'warn'} show={!expense.data.image} />

                        </Grid>


                    </Grid>
                </Container>
            </WithLoader>
        </Master>
    );
};

export default ShowExpense;
