import React, {useEffect} from 'react';
import {Provider} from "react-redux";
import {ThemeProvider} from '@material-ui/core/styles';
import Axios from "axios";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import MomentUtils from '@date-io/moment';

import store from "./store/store";
import userService from './config/user.service';
import {ToastContainer} from "react-toastify";

import {theme} from './config/config';
import {routes} from "./config/routes";
import {NOT_CONNECTED} from "./config/urls";
import {addUser} from "./store/actionsCreators";
import {Protected} from "./utils/auth";

import './styles/app.scss';
import './utils/auth';

import 'moment-timezone';
import 'moment/locale/es';

function App() {


    useEffect(() => {
        if(userService.getToken() && !window.location.pathname.includes(NOT_CONNECTED)) {
            Axios.get('/user').then(res => {
                store.dispatch(addUser(res.data));
            });
        }
    }, []);

    return (
        <>
            <Provider store={store}>
                <MuiPickersUtilsProvider utils={MomentUtils} locale={'es'}>
                <ToastContainer position="bottom-right" closeButton={false} hideProgressBar={true}/>
                <ThemeProvider theme={theme}>

                    <Router>
                        <Switch>
                            {
                                routes.map(route => (
                                    !route.private ?
                                    <Route key={route.name} path={route.path}
                                       routeDef={route} routes={routes}
                                       component={route.component} exact={!!route.exact }/> :
                                    <Protected key={route.name} path={route.path}
                                                routeDef={route} routes={routes}
                                                component={route.component} exact={!!route.exact }/>
                                ))
                            }
                        </Switch>
                    </Router>
                </ThemeProvider >
                </MuiPickersUtilsProvider>
            </Provider>
        </>
    );
}



export default App;

