import React from "react";

const status = (number, upper = false) => {
    let st = 'pendiente';
    switch (number) {
        case 1: st = 'pendiente'; break;
        case 2: st = 'cobrado'; break;
        case -1: st =  'mora'; break;
        default: st = 'pendiente'; break;
    }
    if (upper) st = st.toUpperCase();
    return st;
};

const elStatus = number => {
    const st = status(number);
  return (<div className={`st ${st}`}>{st.toUpperCase()}</div>);
};

const getPin = (st, cobro = 'diario') => {
    return `/img/pin/${cobro.toLowerCase()}/${status(st)}.png`;
};


export default {
    status, elStatus, getPin
}
