import React, {useState} from 'react';
import {
    Button, Dialog,
    DialogActions,
    DialogContent,
    Container,
    Grid,
    DialogTitle,
    useMediaQuery,
    useTheme, InputLabel, Input, InputAdornment, IconButton, FormControl
} from "@material-ui/core";
import {VisibilityOff, Visibility} from '@material-ui/icons';
import PropTypes from 'prop-types';
import useHttp from "../../../utils/useHttp";
import {toast} from "react-toastify";

const DlgPassword = ({title, open, onClose}) => {
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const [values, setValues] = useState({showPassword: false});
    const [data, setData] = useState({
        password: '',
        password_now: ''
    });

    const password = useHttp('post');

    const handleClickShowPassword = () => {
        setValues({ ...values, showPassword: !values.showPassword });
    };

    const onChange = (e) => {
        const target = e.target;
        const value = target.value;
        const name = target.name;
        setData({
            ...data,
            [name]: value});
    };

    const changePw = () => {
        if (!data.password || !data.password_now) {
            toast.error('Complete el formulario');
            return;
        }

        password.request(`/user/password`, data).then(res => {
            if (res.ok) {
                toast.success('Contrasea cambiada con xito');
                onClose();
            }
        }).catch(err => toast.error(err));
    };

    return (
        <Dialog open={open}
                onClose={onClose} aria-labelledby="form-dialog-title" fullScreen={fullScreen}>
            <DialogTitle id="form-dialog-title">{title}</DialogTitle>
            <DialogContent>
                <Container>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <FormControl fullWidth>
                                <InputLabel htmlFor="standard-adornment-password">Contrasea actual</InputLabel>
                                <Input
                                    id="standard-adornment-password"
                                    type={values.showPassword ? 'text' : 'password'}
                                    value={data.password_now !== null ? data.password_now : ''}
                                    name={'password_now'}
                                    onChange={e => onChange(e)}
                                    fullWidth
                                    required
                                    endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                                onClick={handleClickShowPassword}>
                                                {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                        </InputAdornment>
                                    }
                                />
                            </FormControl>
                        </Grid>
                        <Grid item xs={12}>
                            <FormControl fullWidth>
                                <InputLabel htmlFor="standard-adornment-password">Contrasea nueva</InputLabel>
                                <Input
                                    id="standard-adornment-password-2"
                                    type={values.showPassword ? 'text' : 'password'}
                                    value={data.password !== null ? data.password : ''}
                                    name={'password'}
                                    onChange={e => onChange(e)}
                                    fullWidth
                                    required
                                    endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                                onClick={handleClickShowPassword}>
                                                {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                        </InputAdornment>
                                    }
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </Container>
            </DialogContent>
            <DialogActions>
                <Button color={'primary'}  onClick={onClose}>
                    Cancelar
                </Button>
                <Button color="secondary" onClick={() => changePw()} disabled={password.loader}>
                    {password.loader ? 'Espere...' :  'Aceptar'}
                </Button>
            </DialogActions>
        </Dialog>
    );
};

DlgPassword.propTypes = {
    title: PropTypes.string,
    onClose: PropTypes.func.isRequired,
    open: PropTypes.bool
};

DlgPassword.defaultProps = {
    title: 'Cambiar contrasea'
};

export default DlgPassword;
