import React, {useState} from 'react';
import {Button, IconButton, Tooltip} from "@material-ui/core";
import {Photo} from "@material-ui/icons";
import PropTypes from 'prop-types';
import DlgViewImg from "../_modals/DlgViewImg";

const BtnShowImg = ({img, title, iconButton}) => {
    const [openDialog, setOpenDialog] = useState(false);

    const openDialogImg = () => {
        setOpenDialog(true);
    };

    return (
        <>

                {
                    iconButton &&
                    <Tooltip title={title}>
                        <IconButton onClick={() => openDialogImg()}
                                                 aria-label="Detalle">
                            <Photo/>
                        </IconButton>
                    </Tooltip>
                }

                { !iconButton &&
                    <Button
                        onClick={() => openDialogImg()}
                        variant="text"
                        size={'small'}
                        color="default"
                        startIcon={<Photo/>}
                    >{title}</Button>
                }


            <DlgViewImg img={img}
                        title={title}
                        isOpen={openDialog}
                        onClose={() => setOpenDialog(false)} />
        </>
    );
};

BtnShowImg.propTypes = {
    img: PropTypes.string.isRequired,
    title: PropTypes.string,
    iconButton: PropTypes.bool,
};

BtnShowImg.defaultProps = {
    title: 'Ver Imgen',
    iconButton: false
};

export default BtnShowImg;
