import React, {Component} from 'react';
import Autocomplete from "@material-ui/lab/Autocomplete/Autocomplete";
import {CircularProgress, TextField} from "@material-ui/core";
import Axios from "axios";
import PropTypes from 'prop-types';

class SelectZone extends Component {

    constructor(props) {
        super(props);

        this.state = {
            zones: [],
            loader: false,
            open: false,
            value: ''
        }
    }

    getZones() {
        this.setState({...this.state, open: true, loader: true});
        if (this.state.zones.length <= 0) {
            Axios.get('/route').then(res => {
                if (res.ok) {
                    this.setState({...this.state, zones: res.data})
                }
            }).finally(() => this.setState({loader: false}));
        } else {
            this.setState({loader: false});
        }
    }

    reset() {
        this.setState({...this.state, value:''});
    }

    selected(o, v) {
        if(v) {
            this.setState({...this.state, value: v});
        } else {
            this.setState({...this.state, value: ''});
        }
    }

    render() {
        return (
            <div>
                <Autocomplete
                    id="zone_id"
                    open={this.state.open}
                    onOpen={() => this.getZones()}
                    onClose={() => this.setState({...this.state, open: false})}
                    options={this.state.zones}
                    loading={this.state.loader}
                    onChange={(option, value) => this.props.onChange(value ? value.id : value)}
                    loadingText={'Cargando zonas'}
                    inputValue={this.state.value}
                    onInputChange={(v,r) => this.selected(v, r)}
                    getOptionLabel={option => option ? (option.name).toUpperCase() : this.state.value }

                    renderInput={params => (
                        <TextField
                            {...params}
                            required
                            label="Zonas/Rutas" variant="standard" fullWidth
                            InputProps={{
                                ...params.InputProps,
                                endAdornment: (
                                    <React.Fragment>
                                        {this.state.loader ? <CircularProgress color="inherit" size={20} /> : null}
                                        {params.InputProps.endAdornment}
                                    </React.Fragment>
                                ),
                            }}/>
                    )}
                />
            </div>
        );
    }
}

SelectZone.propTypes = {
    onChange: PropTypes.func
};

SelectZone.defaultProps = {
};

export default SelectZone;
