import React from 'react';
import MUIDataTable from "mui-datatables";
import Axios from "axios";
import {toast} from "react-toastify";
import {Grid, Paper} from '@material-ui/core';
import WithLoader from "../_partials/hoc/WithLoader";
import {tableMinOptions} from "../../config/config";
import {processErrors} from "../../utils/utils";

class DetailEmploy extends React.Component {

    constructor (props) {
        super(props);
        this.state = {
            loader: false,
            data: null,
        };
    }

    componentDidMount() {
        this.loadData();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.employ !== prevProps.employ) {
            this.loadData();
        }
    }


    loadData () {
        this.setLoader(true);
        if(this.props.employ !== null) {
            let url = `/employ/info/${this.props.employ}`;
            if (this.props.now!==null) url = `/employ/info/${this.props.employ}?now=${this.props.now}`;

            Axios.get(url).then(res => {
                if(res.ok) {
                    this.setState({
                        data: res.data
                    })
                }
            }).catch(err => {
                const msg = processErrors(err);
                toast.error(msg);
            }).finally(() => this.setLoader(false));
        } else {
            this.setState({
                data: null
            })
        }
    }

    setLoader (value) {
        this.setState({
            loader: value
        });
    }

    render() {
        return (
            <>
                <WithLoader loader={this.state.loader} text={'Cargando datos de empleado...'}>
                    {
                        this.state.data !== null &&
                        <Grid container spacing={2}>
                            {this.state.data.pays.length > 0 && <Grid item xs={12} md={this.props.md}>
                                <MUIDataTable
                                    title={`Pagos de sueldo (${this.state.data.pays.length})`}
                                    data={this.state.data.pays}
                                    columns={[
                                        {label: 'CONCEPTO', name: 'concept'},
                                        {label: 'FECHA', name: 'date'},
                                        {label: 'TOTAL', name: 'total'},
                                    ]}
                                    options={tableMinOptions}
                                />
                            </Grid>}
                            {this.state.data.credits.length >0 && <Grid item xs={12} md={this.props.md}>
                                <MUIDataTable
                                    title={`Crditos (${this.state.data.credits.length})`}
                                    data={this.state.data.credits}
                                    columns={[
                                        {label: 'PLAZO', name: 'plazo'},
                                        {label: 'COBRO', name: 'cobro'},
                                        {label: 'UTILIDAD', name: 'utilidad'},
                                        {label: 'TOTAL', name: 'total'},
                                    ]}
                                    options={tableMinOptions} />

                            </Grid>}
                            {this.state.data.expenses.length>0 &&
                            <Grid item xs={12} md={this.props.md}>
                                <MUIDataTable
                                    title={`Gatos (${this.state.data.expenses.length})`}
                                    data={this.state.data.expenses}
                                    columns={[
                                        {label: 'CONCEPTO', name: 'category'},
                                        {label: 'FECHA', name: 'date'},
                                        {label: 'TOTAL', name: 'monto'},

                                    ]}
                                    options={tableMinOptions} />
                            </Grid>
                            }
                            {
                                this.state.data.cobros > 0 &&
                                    <Grid item xs={12} md={this.props.md}>
                                        <Paper style={{padding: 10}} elevation={3}>
                                            <h3>COBRO TOTAL: </h3>
                                            {this.state.data.cobros}
                                        </Paper>
                                    </Grid>
                            }

                        </Grid>
                    }

                </WithLoader>
            </>
        );
    }
};

DetailEmploy.defaultProps = {
    md: 6,
    now: null
};

export default DetailEmploy;
