import React, {Component} from 'react';
import {TextField} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete/Autocomplete";
import {cobros} from "../../../config/config";
import PropTypes from "prop-types";

class SelectCobro extends Component {
    constructor(props) {
        super(props);

        this.state = {
            value: ''
        }
    }

    reset() {
        this.setState({value: ''});
    }

    selected(e, v) {
        if(v) {
            this.setState({...this.state, value: v});
        } else {
            this.setState({...this.state, value: ''});
        }
    }

    render() {
        return (
            <div>
                <Autocomplete
                    id="cobros"
                    options={cobros}
                    onChange={(option, value) => this.props.onChange(value ? value.name: value)}
                    onInputChange={(v, o) => this.selected(v, o)}
                    inputValue={this.state.value}
                    getOptionLabel={option => option ? (option.value).toUpperCase() : this.state.value.value }
                    renderInput={params => (
                        <TextField
                            {...params}
                            label="Recaudacin" variant="standard" fullWidth />
                    )}
                />
            </div>
        );
    }
}

SelectCobro.propTypes = {
    onChange: PropTypes.func
};

SelectCobro.defaultProps = {
};

export default SelectCobro;
