import React, {Component} from 'react';
import Autocomplete from "@material-ui/lab/Autocomplete/Autocomplete";
import {CircularProgress, TextField} from "@material-ui/core";
import Axios from "axios";
import PropTypes from 'prop-types';

class SelectEmploy extends Component {

    constructor(props) {
        super(props);

        this.state = {
            employs: [],
            loader: false,
            open: false,
            value: ''
        }
    }

    getEmploys() {
        this.setState({...this.state, open: true, loader: true});
        if (this.state.employs.length <= 0) {
            Axios.get('/employ').then(res => {
                if (res.ok) {
                    this.setState({...this.state, employs: res.data})
                }
            }).finally(() => this.setState({loader: false}));
        } else {
            this.setState({loader: false});
        }
    }

    reset() {
        this.setState({...this.state, value:''});
    }

    selected(o, v) {
        if(v) {
            this.setState({...this.state, value: v});
        } else {
            this.setState({...this.state, value: ''});
        }
    }

    render() {
        return (
            <div>
                <Autocomplete
                    id="employ_id"
                    open={this.state.open}
                    onOpen={() => this.getEmploys()}
                    onClose={() => this.setState({...this.state, open: false})}
                    options={this.state.employs}
                    loading={this.state.loader}
                    onChange={(option, value) => this.props.onChange(value)}
                    loadingText={'Cargando empleados'}
                    inputValue={this.state.value}
                    onInputChange={(v,r) => this.selected(v, r)}
                    getOptionLabel={option => option ? ( option.name+' '+option.surname).toUpperCase() : this.state.value }

                    renderInput={params => (
                        <TextField
                            {...params}
                            required
                            label="Empleado" variant="standard" fullWidth
                            InputProps={{
                                ...params.InputProps,
                                endAdornment: (
                                    <React.Fragment>
                                        {this.state.loader ? <CircularProgress color="inherit" size={20} /> : null}
                                        {params.InputProps.endAdornment}
                                    </React.Fragment>
                                ),
                            }}/>
                    )}
                />
            </div>
        );
    }
}

SelectEmploy.propTypes = {
    onChange: PropTypes.func
};

SelectEmploy.defaultProps = {
};

export default SelectEmploy;