import React from 'react';
import {Paper, Grid, TextField} from "@material-ui/core";

const PayCobroCredit = ({plazo, cobro, description}) => {
    return (
        <Paper style={{padding: '1rem', borderRadius: 0}}>
            <div className={'title-muted'}>Datos de pagos y cobros</div>
            <Grid container style={{marginTop: '1rem'}} spacing={1}>
                <Grid item xs={6}>
                    <TextField readOnly label={'Plazo'}
                               value={ plazo ? plazo: 'Desconocido'}/>
                </Grid>
                <Grid item xs={6}>
                    <TextField readOnly label={'Periodo de Cobro'} value={ cobro ? cobro: 'Desconocido'} />
                </Grid>
                <Grid item xs={12}>
                    <TextField readOnly label={'Detalle'} multiline value={ description ? description: 'Ninguna'} fullWidth/>
                </Grid>
            </Grid>
        </Paper>
    );
};

export default PayCobroCredit;
