import React from 'react';
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import Master from "../Master";
import FrmClient from "./_form/FrmClient";

const RegisterClient = () => {
    return (
        <>
            <Master>
                <Container maxWidth='md' component={Paper} className='paper-content'>
                    <div className='paper-header'>
                        <h4> <Icon>person_add</Icon> Agregar Cliente</h4>
                        <Divider />
                    </div>
                    <FrmClient method={'post'} />
                </Container>
            </Master>
        </>
    );
};

export default RegisterClient;
