import React from 'react';
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import Master from "../Master";
import FrmZone from "./_form/FrmZone";
import useGet from "../../utils/useGet";
import WithLoader from "../_partials/hoc/WithLoader";
import WithAdminContent from "../_partials/hoc/WithAdminContent";

const UpdateZone = ({match, history}) => {

    const id = match.params.id;
    const zone = useGet(`/route/${id}`);

    return (
        <Master>
            <WithLoader loader={zone.loader} text={'Cargando zona...'}>
                <WithAdminContent>
                    <Container maxWidth='sm' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>map</Icon> Actualizar Ruta/Zona</h4>
                            <Divider />
                        </div>
                        <FrmZone method={'put'} url={`/route/${id}`} initialZone={zone.data} />
                    </Container>
                </WithAdminContent>
            </WithLoader>
        </Master>
    );
};

export default UpdateZone;
