import React, {useEffect, useState} from 'react';
import MUIDataTable from "mui-datatables";
import {Chip, Icon, IconButton, Tooltip, Button} from "@material-ui/core";
import {Add} from "@material-ui/icons";
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import {EXPENSE_ADD, EXPENSE_SHOW, toName} from "../../config/urls";
import WithLoader from "../_partials/hoc/WithLoader";
import Master from "../Master";
import {money, tableOptions} from "../../config/config";
import useHttp from "../../utils/useHttp";
import Axios from "axios";
import {processErrors, toDate} from "../../utils/utils";
import ExpenseFilter from "./_partials/ExpenseFilter";
import ExpenseInfo from "./_partials/ExpenseInfo";
import WithAdmin from "../_partials/hoc/WithAdmin";

const ListExpenses = () => {

    const [expenses, setExpenses] = useState([]);
    const [total, setTotal] = useState(0);
    const [loader, setLoader] = useState(false);
    const cancel = useHttp('delete');
    const columns = [
        {name: "id", label: 'ID', options: {display: 'false', filter: false}},
        {
            name: 'name', label: 'EMPLEADO',
            options: {
                filter: false,
                customBodyRender: (value, tableMeta) => {
                    return value.toUpperCase();}
            }
        },
        {name: "monto", label: 'MONTO', options: {
            filter: false,
            align: 'right',
                setCellHeaderProps: () => ({ align: 'right' }),
                setCellProps: () => ({ align: 'right' }),
                customBodyRender: (value, tableMeta) => {
                    return <b>{money(value)}</b>}}},
        {name: "date", label: 'FECHA', options: { filter: false, customBodyRender: (value) => {return toDate(value);}}},
        {name: 'category', label: 'CATEGORA', options: {filter: true}},
        {name: 'description', label: 'DETALLE', options: {filter: false, customBodyRender: (value, tableMeta, updateValue) => {
                    return (<small>{value ? value: 'Ninguna'}</small>)
                }}},
        {
            name: 'status', label: 'ESTADO', options: {
                filter: true,
                customFilterListOptions: { render: v => `Estado: ${v}` },

                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            { value === 1 &&
                            <>
                                <Chip
                                    label={"Activo"}
                                    className={'bg-green'}/>
                            </>
                            }
                            { value === 0 &&
                            <>
                                <Chip
                                    label="Anulado"
                                    className={'bg-red'}/>
                            </>
                            }
                        </div>
                    );
                }
            }
        },
        {
            label: 'OPCIONES', name: 'id',
            options: {
                filter: false,
                sort: false,
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        <div>
                            <Tooltip title={'Ver detalles'}>
                                <IconButton component={Link}
                                            to={toName(EXPENSE_SHOW, {id: value})}
                                            size={"small"}
                                            aria-label="Detalle">
                                    <Icon>chrome_reader_mode</Icon>
                                </IconButton>
                            </Tooltip>
                            <Tooltip title={'Anular Gasto'}>
                                <IconButton
                                    onClick={() => cancelExpense(value)}
                                            size={"small"}
                                            aria-label="Anular">
                                    <Icon>block</Icon>
                                </IconButton>
                            </Tooltip>
                        </div>
                    );
                },
            }
        }
    ];

    const [filters, setFilters] = useState({
        page: 0,
        rowPerPage: 10,
        dateFrom: null,
        dateTo: null
    });

    const cancelExpense = (expenseId) => {
        let description = window.prompt('Est seguro que desea anular este gasto?');
        if (!description) return;

        cancel.request(`/expense/${expenseId}`, {description: description})
            .then(res => {
                if (res.ok) {
                    toast.success(res.message);

                    if (expenses){
                        let index = expenses.findIndex(p => p.id === expenseId);
                        expenses[index].status = 0;
                        setExpenses([]);
                        setExpenses(expenses);
                    }
                }
            }).catch(err => {
            toast.error(err);
        });

    };

    const onFilter = (filterList) => {
        setFilters({
            ...filters,
            dateFrom: filterList.dateFrom,
            dateTo: filterList.dateTo,
            employ_id: filterList.employ_id
        })
    };

    useEffect(() => {
        const getExpenses =  () => {
            let url = `/expense?page=${(filters.page+1)}&limit=${filters.rowPerPage}`;
            if(filters.dateFrom && filters.dateTo)
                url+= `&from=${filters.dateFrom.format('YYYY-MM-DD')}&to=${filters.dateTo.format('YYYY-MM-DD')}`;
            if (filters.employ_id) {
                url+= `&employ_id=${filters.employ_id}`;
            }

            setLoader(true);
            Axios.get(url).then(res => {
                if(res.ok) {
                    setExpenses(res.data);
                    setTotal(res.total);
                }
            })
                .catch(err => toast.error(processErrors(err)))
                .finally(() => setLoader(false))
        };

        getExpenses();
    }, [filters]);

    const options = {
        ...tableOptions,
        rowsPerPage: filters.rowPerPage,
        page: filters.page,
        count: total,
        serverSide: true,
        elevation: 0,
        sort: false,
        filter: false,
        onTableChange: (action, tableState) => {
            switch (action) {
                case 'changeRowsPerPage':
                    setFilters({...filters, rowPerPage: tableState.rowsPerPage});

                    break;
                case 'changePage':
                    setFilters({...filters, page: tableState.page});
                    break;
                default:
                    console.log(action);
                    break;
            }
        },
    };

    return (
        <Master>
            <WithAdmin>
                <ExpenseInfo/>
            </WithAdmin>
            <ExpenseFilter
                onClearFilters={() => setFilters({...filters, dateFrom: null, dateTo: null, employ_id: null})}
                filters={filters}
                onFilter={(v) => onFilter(v)}/>
            <WithLoader loader={loader}>
                <MUIDataTable
                    title={<HeaderTitle/>}
                    data={expenses}
                    columns={columns} options={options} />
            </WithLoader>
        </Master>
    );
};

const HeaderTitle = () => {
    return (
        <div>
            <Button component={Link} to={EXPENSE_ADD}>
                <Add /> &nbsp;Agregar
            </Button>
        </div>
    );
};

export default ListExpenses;
