import React from 'react';
import Master from "../Master";
import WithLoader from "../_partials/hoc/WithLoader";
import {Container, Divider, Icon, Paper} from "@material-ui/core";
import useGet from "../../utils/useGet";
import FrmClient from "./_form/FrmClient";

const EditClient = ({match}) => {

    const id = match.params.id;

    const client = useGet(`/client/${id}`);

    return (
        <>
            <Master>
                <WithLoader loader={client.loader} text={'Cargando datos del empleado...'}>
                    <Container maxWidth='md' component={Paper} className='paper-content'>
                        <div className='paper-header'>
                            <h4> <Icon>person_edit</Icon> Editar Cliente</h4>
                            <Divider />
                        </div>
                        {
                            client.data.name !== undefined &&
                            <FrmClient method={'put'} url={`/client/${id}`}
                                       initialData={client.data}/>
                        }
                    </Container>
                </WithLoader>
            </Master>
        </>
    );
};

export default EditClient;
