export default {

    setSession({ token }) {
        localStorage.setItem('token',token);
    },

    clearSession() {
        if(localStorage.getItem('token')) {
            localStorage.removeItem('token');
            window.location.href = "/login";
        }
    },

    getToken() {
        return (localStorage.getItem('token') === undefined ? null : localStorage.getItem('token'));
    },

}
