import React from 'react';
import Master from "../Master";
import useGet from "../../utils/useGet";
import {
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow, IconButton, Icon, Divider, Button
} from "@material-ui/core";
import {Link} from "react-router-dom";
import useHttp from "../../utils/useHttp";
import {toast} from "react-toastify";
import WithLoader from "../_partials/hoc/WithLoader";
import WithAdmin from "../_partials/hoc/WithAdmin";
import {toName, ZONE_ADD, ZONE_EDIT} from "../../config/urls";

const ListZone = ({history}) => {

    const zones = useGet('/route');
    const httpDelete = useHttp('delete');

    const deleteZone = (id) => {
        const confirm = window.confirm('Estas seguro que deseas eliminar esta zona?');
        if(!confirm) return;

        httpDelete.request(`/route/${id}`).then(res => {
            if(res.ok){
                const newZones = zones.data.filter(e => e.id !== id);
                zones.setData(newZones);
                toast.success('Ruta eliminada con xito');
            } else toast.error('No se ha podido eliminar la ruta');
        }).catch(e => {
            toast.error(e);
        })
    };
    return (
        <Master>
            <WithLoader loader={zones.loader}>
                <TableContainer component={Paper} className="paper-content">
                    <div className="paper-header">
                        <div className="paper-header-ancle">
                            <h4><Icon>list</Icon> Lista de rutas</h4>
                            <WithAdmin>
                                <Button to={ZONE_ADD} component={Link} variant={"text"} color={"secondary"}>
                                    <Icon>add</Icon> Nueva ruta
                                </Button>
                            </WithAdmin>
                        </div>
                        <Divider />
                    </div>
                    <Table size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell>Nombre</TableCell>
                                <TableCell>Descripcin</TableCell>
                                <WithAdmin>
                                    <TableCell>Opciones</TableCell>
                                </WithAdmin>
                            </TableRow>
                        </TableHead>

                        <TableBody>
                            {zones.data.map(z => (
                                <TableRow key={z.id}>
                                    <TableCell>
                                        {z.name}
                                    </TableCell>
                                    <TableCell>
                                        {z.description === null ? 'Ninguna' : z.description }
                                    </TableCell>
                                    <WithAdmin>
                                        <TableCell>
                                            <IconButton component={Link}
                                                        to={toName(ZONE_EDIT, {id: z.id})}
                                                        size={"small"}
                                                        aria-label="Editar">
                                                <Icon>edit</Icon>
                                            </IconButton>
                                            <IconButton onClick={ e => deleteZone(z.id)}
                                                        size={"small"}
                                                        aria-label="delete">
                                                <Icon>delete</Icon>
                                            </IconButton>
                                        </TableCell>
                                    </WithAdmin>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </WithLoader>
        </Master>
    );
};

export default ListZone;
