import React from 'react';
import {Grid} from "@material-ui/core";
import {Skeleton} from "@material-ui/lab";

const SKShowCredit = ({children, loader}) => {


    if (loader) {
        return (
            <Grid container spacing={1}>
                <Grid item xs={12} md={12}>
                    <Grid container>
                        <Grid item xs={6} sm={4} md={3}><Skeleton style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}} variant="rect" height={79} /></Grid>
                        <Grid item xs={6} sm={4} md={2}><Skeleton style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}} variant="rect" height={79} /></Grid>
                        <Grid item xs={6} sm={4} md={3}><Skeleton style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}} variant="rect" height={79} /></Grid>
                        <Grid item xs={6} sm={4} md={2}><Skeleton style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}} variant="rect" height={79} /></Grid>
                        <Grid item xs={6} sm={4} md={2}><Skeleton style={{margin: '0 .1rem .2rem .1rem', borderRadius: 0}} variant="rect" height={79} /></Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Grid container spacing={1}>
                        <Grid item xs={12} md={6}>
                            <Skeleton variant="rect" height={300} />
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Skeleton variant="rect" height={300} />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Skeleton variant="rect" height={100} />
                </Grid>
            </Grid>

        );
    }

    else return children;
};

SKShowCredit.defaultProps = {
    loader: false
};

export default SKShowCredit;
