import React, {useState} from 'react';
import PropTypes from 'prop-types';
import WithMapKey from "../../maps/WithMapKey";
import {InfoWindow, Map, Marker} from "google-maps-react";
import PayModel from "../../../models/PayModel";
import BtnsPayOptions from "../_partials/BtnsPayOptions";

const PayMap = ({payments, google, height, zoom, exec}) => {

    const [state, setState] = useState({
        showingInfoWindow: false,
        activeMarker: {},
        selectedPlace: {},
        selectedPay: null
    });

    const onMarkerClick = (props, marker, pay) =>
        setState({
            ...state,
            selectedPlace: props,
            activeMarker: marker,
            showingInfoWindow: true,
            selectedPay: pay});

    const onMapClicked = (props) => {
        if (state.showingInfoWindow) {
            setState({
                ...state,
                showingInfoWindow: false,
                activeMarker: null,
                selectedPay: null
            });
        }
    };


    const displayMarkers = () => {
        return payments.map((store, index) => {
            return <Marker key={store.id} id={store.id}
                           title={`${store.address} ${store.ref_detail ? ' | '+store.ref_detail: ''}`}
                           name={store.client_name}
                           position={{
                            lat: store.lat,
                            lng: store.lon
                           }}
                           icon={{
                               url: PayModel.getPin(store.status, store.cobro),
                               anchor: new google.maps.Point(38,38),
                               scaledSize: new google.maps.Size(42,42)
                           }}
                           onClick={(m,e) => onMarkerClick(m, e, store) } />
        })
    };

    return (
        <div className={'content-google-map'}>
            {payments && payments.length >= 1 &&
                <Map google={google}  onClick={ (p) => onMapClicked(p) }
                     style={{width: '100%', height: height, minHeight: height, position: 'relative'}}
                     className={'google-map-min'}
                     initialCenter={{ lat: payments[0].lat+3, lng: payments[0].lon+12}}
                     zoom={zoom}>
                    {displayMarkers()}
                    <InfoWindow
                        marker={state.activeMarker}
                        visible={state.showingInfoWindow}>
                        <div>
                            {
                                state.selectedPay &&
                                <>
                                    <h4>{state.selectedPay.client_name} {state.selectedPay.client_surname}</h4>
                                    <p>{state.selectedPay.address}</p>
                                    <small>{PayModel.elStatus(state.selectedPay.status)}</small>
                                    {/*ver crdito*/}
                                </>
                            }
                        </div>
                    </InfoWindow>
                </Map>
            }

            {/*BUTTONS*/
                state.selectedPay &&
                <div className={'map-btn'}>
                    <BtnsPayOptions exec={exec} pay={state.selectedPay}/>
                </div>
            }
        </div>
    );
};

PayMap.propTypes = {
    payments: PropTypes.array,
    zoom: PropTypes.number,
    height: PropTypes.string,
    exec: PropTypes.func
};

PayMap.defaultProps = {
    height: '100%',
    zoom: 12
};

export default WithMapKey(PayMap);
